import * as path from 'path';
import * as os from 'os';
import * as fs from 'fs';
import { https } from 'follow-redirects';
import * as cp from 'child_process';
import * as vscode from 'vscode';
import * as Docker from 'dockerode';

import { exists, mkdir } from './pfs';
import { runCommand, showProgress, extensionHostReadyExp, findWindowsExecutable, extensionsToInstallAlwaysKey, ResolverParameters, findDevContainerConfigPath } from './resolverUtils';
import { readDevContainerConfigFile } from './containerResolver';
import { promisify } from 'util';
import { getProduct } from './product';
import { ContainerError } from './errors';

const vschTarFile = 'vscode-server-linux-x64.tar.gz';

export async function getHomedirFromImage(info: Docker.ImageInspectInfo) {
	return getEnvVariable(info, 'HOME') || '/root';
}

export async function getHomedirFromContainer(docker: Docker, containerId: string) {
	const info = await docker.getContainer(containerId)
		.inspect();
	return getEnvVariable(info, 'HOME') || '/root';
}

function getEnvVariable(info: Docker.ImageInspectInfo | Docker.ContainerInspectInfo, variable: string) {
	const home = info.Config.Env.find(e => e.startsWith(`${variable}=`));
	return home && home.substr(variable.length + 1);
}

export async function getInstallLocation(homedir: string): Promise<string> {
	const { commit } = await getProduct();
	return path.posix.join(getUserVSCodeLocation(homedir), 'bin', commit || 'dev');
}

function getExtensionsLocation(homedir: string) {
	return path.posix.join(getUserVSCodeLocation(homedir), 'extensions');
}

export function getUserVSCodeLocation(homedir: string) {
	return path.posix.join(homedir, '.vscode-remote');
}

export async function getNodeLocation(homedir: string): Promise<string> {
	const { commit } = await getProduct();
	return commit ? path.posix.join(await getInstallLocation(homedir), 'node') : path.posix.join(homedir, 'bin/node');
}

export async function installVSCHeadless(params: ResolverParameters, containerId: string, installLocation: string, homedir: string): Promise<void> {
	const { commit, quality, applicationName } = await getProduct();
	const { cwd } = params;
	const devRemoteLocation = path.posix.join(installLocation, '..', 'dev-remote');

	await ensureIsSupportedOS(params, cwd, containerId);

	if (await isFolder(params, cwd, containerId, commit ? installLocation : devRemoteLocation)) {
		await installExtensions(params, containerId, installLocation, homedir);
		return;
	}

	if (commit) {
		params.output.write(`Installing VS Code Server for commit ${commit}\r\n`);
		const tmpInstallLocation = `${installLocation}_${Date.now()}`;
		await dockerExec(params, cwd, containerId, ['mkdir', '-p', tmpInstallLocation]);
		const cached = await isCached(commit);
		const cacheLocation = await createCacheLocation(commit);
		const cachedTarPath = path.join(cacheLocation, vschTarFile);
		if (!cached) {
			try {
				const tmpTarPath = `${cachedTarPath}_${Date.now()}`;
				await new Promise((resolve, reject) => {
					const file = fs.createWriteStream(tmpTarPath);
					const url = `https://update.code.visualstudio.com/commit:${commit}/server-linux-x64/${quality || 'insider'}`;
					const req = https.get(url, res => {
						if (res.statusCode! < 200 || res.statusCode! > 299) {
							reject(new ContainerError({
								description: 'Downloading the VS Code Server failed. Please try again later.',
								originalError: `Failed to download VS Code Server (${url}): HTTP ${res.statusCode} - ${res.statusMessage}`
							}));
							return;
						}
						res.on('error', reject);
						res.pipe(file);
						res.on('end', resolve);
					});
					req.on('error', reject);
				});
				await promisify(fs.rename)(tmpTarPath, cachedTarPath);
			} catch (err) {
				if (err) {
					throw err;
				}
				throw new Error('Failed to download VS Code Server');
			}
		}

		await dockerCp(params, cwd, cachedTarPath, containerId, tmpInstallLocation, 'Copying VS Code server to container');
		await dockerExec(params, cwd, containerId, ['tar', '-xf', 'vscode-server-linux-x64.tar.gz', '--strip-components', '1'], tmpInstallLocation);
		await dockerExec(params, cwd, containerId, ['rm', 'vscode-server-linux-x64.tar.gz'], tmpInstallLocation);
		await dockerExec(params, cwd, containerId, ['mv', '-n', path.posix.join(tmpInstallLocation, '.'), installLocation]);
		await installExtensions(params, containerId, installLocation, homedir);
	} else {
		const { result, pulse } = await ensureIsFolder(params, cwd, containerId, installLocation);
		await showProgress(params, `Check VS Code repository is mounted at '${installLocation}'.`, result, pulse);

		const info = await params.docker.getContainer(containerId)
			.inspect();
		if (info.Config.User !== 'root') {
			for (let folder = path.posix.dirname(devRemoteLocation); folder !== '/' && folder !== homedir; folder = path.posix.dirname(folder)) {
				await dockerExec(params, cwd, containerId, ['chown', info.Config.User, folder], installLocation, { user: 'root' });
			}
		}

		const tmpDevRemoteLocation = `${devRemoteLocation}_${Date.now()}`;
		await dockerExec(params, cwd, containerId, ['cp', '-r', 'remote', tmpDevRemoteLocation], installLocation);
		await dockerExec(params, cwd, containerId, ['./installDevPackagesAsRoot.sh'], tmpDevRemoteLocation, { user: 'root' });
		await dockerExec(params, cwd, containerId, ['./installDevModules.sh'], tmpDevRemoteLocation);
		await dockerExec(params, cwd, containerId, ['ln', '-s', path.posix.join(installLocation, 'resources/server/bin/code-dev.sh'), path.posix.join(homedir, `bin/${applicationName}`)], installLocation);

		await dockerExec(params, cwd, containerId, ['mv', '-n', '-T', tmpDevRemoteLocation, devRemoteLocation], installLocation);
	}
}

async function installExtensions(params: ResolverParameters, containerId: string, installLocation: string, homedir: string) {
	const installExtensionsArgs = await computeExtensionsInstallArgs(params, containerId, homedir);
	if (installExtensionsArgs.length) {
		try {
			await dockerExec(params, params.cwd, containerId, [path.posix.join(installLocation, 'server.sh'), getTelemetryArg(), ...installExtensionsArgs], installLocation);
		} catch (err) {
			// Ignore (already logged to terminal).
		}
	}
}

async function computeExtensionsInstallArgs(params: ResolverParameters, containerId: string, homedir: string) {
	const isUpdating = await isFolder(params, params.cwd, containerId, getExtensionsLocation(homedir));
	const extensionsToInstall = isUpdating ? getLanguagePacksToInstall() : await getExtensionsToInstall(params.cwd);
	if (!extensionsToInstall.length) {
		return [];
	}

	const installArgs = (<string[]>[]).concat(...extensionsToInstall.map(each => ['--install-extension', each]));
	return isUpdating ? installArgs : installArgs.concat(['--force']);
}

async function ensureIsSupportedOS(params: ResolverParameters, hostCwd: string, containerId: string) {
	let id = 'unknown';
	try {
		const { result } = await dockerExec2(params, hostCwd, containerId, ['/bin/sh', '-c', '(cat /etc/os-release || cat /usr/lib/os-release) 2>/dev/null']);
		const { cmdOutput } = await result;
		id = (cmdOutput.match(/^ID=([^\u001b\r\n]*)/m) || [])[1] || 'notfound';
	} catch (err) {
		console.error(err);
		// Optimistically continue.
	}
	if (id === 'alpine') {
		throw new ContainerError({
			description: 'Alpine Linux is not supported as the container OS. Try using an image that is not built from Alpine Linux.',
		});
	}
}

export async function runPostCreateCommand(params: ResolverParameters, containerId: string, homedir: string, postCreateCommand?: string | string[], dockerCwd?: string) {
	const markerFile = path.posix.join(getUserVSCodeLocation(homedir), '.postCreateCommandMarker');
	if (await isFile(params, params.cwd, containerId, markerFile)) {
		return;
	}
	await touchFile(params, params.cwd, containerId, markerFile);
	if (postCreateCommand) {
		try {
			await dockerExec(params, params.cwd, containerId, typeof postCreateCommand === 'string' ? ['/bin/sh', '-c', postCreateCommand] : postCreateCommand, dockerCwd);
		} catch (err) {
			// Ignore (already logged to terminal).
		}
	}
}

async function isFile(params: ResolverParameters, hostCwd: string, containerId: string, location: string) {
	try {
		const { result } = await dockerExec2(params, hostCwd, containerId, ['test', '-f', location]);
		await result;
		return true;
	} catch (err) {
		return false;
	}
}

async function touchFile(params: ResolverParameters, hostCwd: string, containerId: string, location: string) {
	await dockerExec(params, hostCwd, containerId, ['touch', location]);
}

async function isFolder(params: ResolverParameters, hostCwd: string, containerId: string, location: string) {
	try {
		const { result } = await ensureIsFolder(params, hostCwd, containerId, location);
		await result;
		return true;
	} catch (err) {
		return false;
	}
}

async function ensureIsFolder(params: ResolverParameters, hostCwd: string, containerId: string, location: string) {
	return dockerExec2(params, hostCwd, containerId, ['test', '-d', location]);
}

async function getExtensionsToInstall(folderPath: string): Promise<string[]> {

	const result: Set<string> = new Set<string>();

	const configPath = await findDevContainerConfigPath(folderPath);
	const config = configPath && await readDevContainerConfigFile(configPath);

	if (config && config.extensions) {
		for (const extension of config.extensions) {
			if (!result.has(extension)) {
				result.add(extension);
			}
		}
	}

	let extensions: string[] = vscode.workspace.getConfiguration().get(extensionsToInstallAlwaysKey) || [];
	for (const extension of extensions) {
		if (!result.has(extension)) {
			result.add(extension);
		}
	}

	for (const extension of getLanguagePacksToInstall()) {
		if (!result.has(extension)) {
			result.add(extension);
		}
	}

	const extensionsToInstall: string[] = [];
	result.forEach(extension => extensionsToInstall.push(extension));
	return extensionsToInstall;
}

function getLanguagePacksToInstall() {
	return vscode.extensions.all.filter(extension => {
		const contributes = extension.packageJSON && extension.packageJSON.contributes;
		return contributes && contributes.localizations ? contributes.localizations.length > 0 : false;
	})
		.map(extension => extension.id);
}

async function isCached(commit: string): Promise<string | undefined> {
	// TODO add random number to vsch and store in global storage
	let cachedTar = path.join(cacheLocation(commit), vschTarFile);
	if (await exists(cachedTar)) {
		return cachedTar;
	}
	return undefined;
}

function cacheLocation(commit: string): string {
	return path.join(path.join(os.tmpdir(), 'vsch'), commit);
}

async function createCacheLocation(commit: string): Promise<string> {
	let cacheDir = path.join(os.tmpdir(), 'vsch');
	try {
		await mkdir(cacheDir);
	} catch (e) {
		// ignore
	}
	let commitDir = path.join(cacheDir, commit);
	try {
		await mkdir(commitDir);
	} catch (e) {
		// ignore
	}
	return commitDir;
}

export async function launchVSCHeadless(params: ResolverParameters, containerId: string, installLocation: string, homedir: string, listenPort: number) {
	const processes = await findProcesses(params.cwd, containerId);
	const rehas = processes.filter(process => new RegExp(`${path.posix.join(path.posix.dirname(installLocation).replace('.', '\\.'), '[^/]*', '(server|launchDevMode)\.sh')}`).test(process.cmd));

	const { commit, applicationName } = await getProduct();
	const suffix = `/${commit || 'dev'}`;
	const { current, others } = rehas.reduce((split, current) => {
		split[current.cwd.endsWith(suffix) ? 'current' : 'others'].push(current);
		return split;
	}, { current: [] as typeof rehas, others: [] as typeof rehas });

	if (others.length) {
		params.output.write('Terminating old extension host agent.\r\n');
		const kill = ['/bin/sh', '-c', `kill -9 ${others.map(other => `-${other.pid}`).join(' ')}`];
		await execAsRoot(params.cwd, containerId, kill);
	}

	if (current.length) {
		params.output.write('Extension host agent is already running.\r\n');
		return;
	}

	await dockerExec(params, params.cwd, containerId, ['/bin/sh', '-c', `command -v git >/dev/null 2>&1 && git config --system credential.helper '!f() { command -v ${applicationName} >/dev/null 2>&1 && ${applicationName} --gitCredential $*; }; f' || true`], installLocation, { user: '0' });

	const containerInfo = await params.docker.getContainer(containerId)
		.inspect();
	const imageInfo = await params.docker.getImage(containerInfo.Image)
		.inspect();
	const shell = (<any>imageInfo.Config).Shell || ['/bin/sh', '-c'];

	if (commit) {
		const cmd = [...shell, `PATH="${installLocation}/bin:$PATH" ${path.posix.join(installLocation, 'server.sh')} ${getTelemetryArg()} --port ${listenPort}`];
		await dockerExec(params, params.cwd, containerId, cmd, installLocation, { resolveOn: extensionHostReadyExp });
	} else {
		const devRemoteLocation = path.posix.join(installLocation, '..', 'dev-remote');
		const installExtensionsArgs = await computeExtensionsInstallArgs(params, containerId, homedir);
		// TODO: Inline script?
		await dockerExec(params, params.cwd, containerId, [...shell, path.posix.join(devRemoteLocation, 'launchDevMode.sh')], devRemoteLocation, {
			dockerEnv: {
				PORT: `${listenPort}`,
				VSCODE_REPO: installLocation,
				VSCODE_TELEMETRY_ARG: getTelemetryArg(),
				extensions: installExtensionsArgs.join(' ')
			},
			resolveOn: extensionHostReadyExp
		});
	}
}

function getTelemetryArg() {
	const enableTelemetry = !!vscode.workspace.getConfiguration().get('telemetry.enableTelemetry');
	return `${enableTelemetry ? '' : '--disable-telemetry'}`;
}

export async function findExtensionHostProcesses(cwd: string, containerId: string) {
	const processes = await findProcesses(cwd, containerId);
	return processes.filter(proc => proc.cmd.indexOf('--type=extensionHost') !== -1);
}

async function findProcesses(cwd: string, containerId: string) {
	const ps = ['/bin/sh', '-c', 'for pid in `cd /proc && ls -d [0-9]*`; do { echo $pid ; readlink -f /proc/$pid/cwd ; xargs -0 < /proc/$pid/cmdline ; } | tr "\n" " " ; echo ; done 2>/dev/null'];
	const { stdout } = await execAsRoot(cwd, containerId, ps);

	const n = 3;
	const processes = stdout.split('\n')
		.map(line => line.split(' '))
		.filter(parts => parts.length >= n)
		.map(parts => [...parts.slice(0, n - 1), parts.slice(n - 1).join(' ')])
		.map(parts => ({
			pid: parts[0],
			cwd: parts[1],
			cmd: parts[2]
		}));
	return processes;
}

async function execAsRoot(cwd: string, containerId: string, cmd: string[]) {
	const docker = await findWindowsExecutable('docker', cwd, process.env);
	return promisify(cp.execFile)(docker, ['exec', '-u', '0', containerId, ...cmd], { cwd })
		.catch(result => result as { stdout: string; stderr: string; });
}

async function dockerCp(params: ResolverParameters, cwd: string, src: string, containerId: string, dst: string, progressMessage: string): Promise<void> {
	const args = ['cp', src, `${containerId}:${dst}`];
	const { result, pulse } = await runCommand(cwd, 'docker', args, params.output);
	await showProgress(params, progressMessage, result, pulse);
}

async function dockerExec(params: ResolverParameters, hostCwd: string, containerName: string, cmd: string[], dockerCwd?: string, options?: { dockerEnv?: Record<string, string>, env?: Record<string, string>, resolveOn?: RegExp, user?: string }) {
	const { result } = await dockerExec2(params, hostCwd, containerName, cmd, dockerCwd, options);
	return result;
}

async function dockerExec2(params: ResolverParameters, hostCwd: string, containerName: string, cmd: string[], dockerCwd: string = '', options?: { dockerEnv?: Record<string, string>, env?: Record<string, string>, resolveOn?: RegExp, user?: string }) {
	const args = ['exec'];
	if (dockerCwd) {
		args.push('-w', dockerCwd);
	}
	if (options && options.user) {
		args.push('-u', options.user);
	}
	if (options && options.dockerEnv) {
		const env = options.dockerEnv;
		for (const key in env) {
			args.push('-e', `${key}=${env[key]}`);
		}
	}
	args.push(containerName, ...cmd);
	return runCommand(hostCwd, 'docker', args, params.output, options);
}
