import { ContainerError } from './errors';

export function substitute(context: string, value: any): any {
	if (typeof value === 'string') {
		return resolveString(context, value);
	} else if (Array.isArray(value)) {
		return value.map(s => substitute(context, s));
	} else if (value && typeof value === 'object') {
		const result: any = Object.create(null);
		Object.keys(value).forEach(key => {
			result[key] = substitute(context, value[key]);
		});
		return result;
	}
	return value;
}

const isWindows = process.platform === 'win32';

const VARIABLE_REGEXP = /\$\{(.*?)\}/g;

let getEnv0: Record<string, string | undefined>; // Other names trigger minifier bug?
function getEnv() {
	if (!getEnv0) {
		if (isWindows) {
			getEnv0 = Object.create(null);
			Object.keys(process.env).forEach(key => {
				getEnv0[key.toLowerCase()] = process.env[key];
			});
		} else {
			getEnv0 = process.env;
		}
	}
	return getEnv0;
}

function resolveString(context: string, value: string): string {
	// loop through all variables occurrences in 'value'
	return value.replace(VARIABLE_REGEXP, evaluateSingleVariable.bind(undefined, context));
}

function evaluateSingleVariable(context: string, match: string, variable: string): string {

	// try to separate variable arguments from variable name
	let argument: string | undefined;
	const parts = variable.split(':');
	if (parts.length > 1) {
		variable = parts[0];
		argument = parts[1];
	}

	switch (variable) {

		case 'env':
			if (argument) {
				if (isWindows) {
					argument = argument.toLowerCase();
				}
				const env = getEnv()[argument];
				if (typeof env === 'string') {
					return env;
				}
				// For `env` we should do the same as a normal shell does - evaluates missing envs to an empty string #46436
				return '';
			}
			throw new ContainerError({
				description: `'${match}' in ${context} can not be resolved because no environment variable name is given.`
			});

		default:
			return match;
	}
}
