import * as Docker from 'dockerode';
import * as vscode from 'vscode';
import * as os from 'os';

import { runCommand, showProgress, startEventSeen, defaultDevPort, containerRemoteContext, ResolverParameters, printPortInfo, getConfigFilePath, eventOrStatus, runCommandInDetachedProcess } from './resolverUtils';
import { getInstallLocation, installVSCHeadless, launchVSCHeadless, getNodeLocation, getHomedirFromContainer, findExtensionHostProcesses, runPostCreateCommand } from './injectHeadless';
import { forwardPort } from './portForwarding';
import { promisify } from 'util';
import { Server } from 'net';
import { ContainerError } from './errors';

export interface DevContainerFromDockerComposeConfig {
	configFilePath: string;
	dockerComposeFile: string | string[];
	service: string;
	workspaceFolder: string;
	name?: string;
	devPort?: number;
	extensions?: string[];
	shutdownAction?: 'none' | 'stopCompose';
	postCreateCommand?: string | string[];
}

export async function openDockerComposeDevContainer(params: ResolverParameters, config: DevContainerFromDockerComposeConfig): Promise<vscode.ResolvedAuthority> {

	let container: Docker.ContainerInfo | undefined;
	try {

		container = await findTargetContainer(params.docker, config.service);
		if (!container || container.State !== 'running') {
			container = await startContainer(params, config, !container);
		}
		params.containerId = container.Id;

		const homedir = await getHomedirFromContainer(params.docker, container.Id);
		const installLocation = await getInstallLocation(homedir);
		const nodeLocation = await getNodeLocation(homedir);

		await installVSCHeadless(params, container.Id, installLocation, homedir);
		await runPostCreateCommand(params, container.Id, homedir, config.postCreateCommand);

		const containerPort = config.devPort || defaultDevPort;
		await launchVSCHeadless(params, container.Id, installLocation, homedir, containerPort);

		let server: Server;
		let hostPort: number | undefined;
		if (config.devPort) {
			const exposed = container.Ports.find(port => port.PrivatePort === containerPort);
			if (exposed) {
				hostPort = exposed.PublicPort;
			}
		}
		if (!hostPort) {
			server = await forwardPort(params.forwardedPorts, params.cwd, container.Id, containerPort, nodeLocation);
			hostPort = server.address().port;
		}

		printPortInfo(params.output, hostPort, container);

		const cont = await params.docker.getContainer(container.Id);
		const shutdown = async (rebuild?: boolean) => {
			vscode.commands.executeCommand('setContext', containerRemoteContext, false);

			if (rebuild) {
				const removed = eventOrStatus(params.docker, cont.id, 'die');
				await cont.remove({ force: true });
				await removed;
			} else if ((!config.shutdownAction || config.shutdownAction === 'stopCompose') && (await findExtensionHostProcesses(params.cwd, cont.id)).length <= 1) {
				const args = [];
				getDockerComposeFilePaths(params.cwd, config)
					.forEach((composeFile: string) => args.push('-f', composeFile));
				args.push('stop', '-t', '0');
				runCommandInDetachedProcess(params.cwd, 'docker-compose', args);
			}
			if (server) {
				await promisify(server.close.bind(server))();
			}
		};

		vscode.commands.executeCommand('setContext', containerRemoteContext, true);
		params.shutdowns.push(shutdown);
		return new vscode.ResolvedAuthority('localhost', hostPort);

	} catch (err) {
		if (err instanceof ContainerError) {
			err.manageContainer = !!container;
			err.container = container;
			throw err;
		}
		throw new ContainerError({
			description: 'An error occurred setting up the container.',
			originalError: err,
			manageContainer: !!container,
			container
		});
	}
}

async function startContainer(params: ResolverParameters, config: DevContainerFromDockerComposeConfig, rebuild: boolean): Promise<Docker.ContainerInfo> {
	const docker = new Docker();

	let cancel: () => void;
	const canceled = new Promise<void>((_, reject) => cancel = reject);
	const { started } = await startEventSeen(docker, 'com.docker.compose.service', config.service, canceled); // await getEvents, but only assign started.

	const isWindows = os.platform() === 'win32';
	const PWD = isWindows ? toLinuxVMPath(params.cwd) : params.cwd;
	const HOME = (isWindows ? toLinuxVMPath(process.env.USERPROFILE) : process.env.HOME) || PWD; // Falling back to cwd.
	const args = [];
	// If dockerComposeFile is an array, add -f <file> in order. https://docs.docker.com/compose/extends/#multiple-compose-files
	getDockerComposeFilePaths(params.cwd, config).forEach((composeFile: string) => args.push('-f', composeFile));
	args.push('up', '-d');
	if (rebuild) {
		args.push('--build');
	}
	const { result, pulse } = await runCommand(params.cwd, 'docker-compose', args, params.output, { env: { HOME, PWD } });
	try {
		await showProgress(params, `docker-compose up`, result, pulse);
	} catch (err) {
		cancel!();
		throw err;
	}

	await started;
	return (await findTargetContainer(docker, config.service))!;
}

function toLinuxVMPath<T extends string | undefined>(windowsPath: T) {
	if (!windowsPath) {
		return windowsPath;
	}
	const path = vscode.Uri.file(windowsPath!).path;
	return `/host_mnt/${path.substr(1, 1).toLowerCase()}${path.substr(3)}`;
}

async function findTargetContainer(docker: Docker, serviceName: string): Promise<Docker.ContainerInfo | undefined> {
	const list = await docker.listContainers({ all: true, filters: { label: [`com.docker.compose.service=${serviceName}`] } });
	return list && list[0];
}

export function getDockerComposeFilePaths(workspaceFolderPath: string, config: DevContainerFromDockerComposeConfig): string[] {
	if (Array.isArray(config.dockerComposeFile)) {
		return config.dockerComposeFile.map(composeFile => getConfigFilePath(workspaceFolderPath, config, composeFile));
	} else {
		return [getConfigFilePath(workspaceFolderPath, config, config.dockerComposeFile)];
	}
}
