import * as vscode from 'vscode';
import * as path from 'path';
import * as os from 'os';
import * as Docker from 'dockerode';

import { exists } from './pfs';
import { resolve as containerResolve, readDevContainerConfigFile } from './containerResolver';
import { exec, getDevContainerUri, getHostPathFromAuthority, tildify, findDevContainerConfigPath, getDevContainerPaths, IOpenFolderAPICommandOptions, resolver, ResolverResult, INewWindowAPICommandOptions } from './resolverUtils';
import { getDockerfilePath } from './singleContainer';
import { DevContainerFromDockerComposeConfig, getDockerComposeFilePaths } from './dockerCompose';
import { resolve as attachResolve } from './attachContainerResolver';
import { handleError } from './errors';
import { createDevContainerConfig, createDevContainerConfigIn } from './createConfiguration';
import { checkDockerVersion, checkDockerCompose } from './dockerVersion';
import { ConfigLinkProvider } from './configurationLinkProvider';
import { forwardPortUI } from './portForwardingUI';

let rebuild = false;

const resolverResults = new Map<string, ResolverResult>();

export function activate(context: vscode.ExtensionContext) {
	vscode.workspace.registerRemoteAuthorityResolver('dev-container', { resolve: resolver(resolverResults, containerResolve) });
	vscode.workspace.registerRemoteAuthorityResolver('attached-container', { resolve: resolver(resolverResults, attachResolve) });

	informUserAboutDevContainer()
		.catch(console.error);

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.openFolder', async () => {
		if (!await checkDockerVersion()) {
			return;
		}
		let defaultUri: vscode.Uri | undefined;
		const folders = vscode.workspace.workspaceFolders;
		if (folders && folders[0] && folders[0].uri.scheme === 'vscode-remote') {
			const folder = getHostPathFromAuthority(folders[0].uri.authority);
			if (folder) {
				defaultUri = vscode.Uri.file(folder);
			}
		}
		if (!defaultUri) {
			defaultUri = vscode.Uri.file(os.homedir());
		}
		const options: vscode.OpenDialogOptions = {
			defaultUri,
			canSelectFiles: false,
			canSelectFolders: true,
			canSelectMany: false,
			openLabel: 'Open' // TODO NLS
		};
		const result = await vscode.window.showOpenDialog(options);
		if (result) {
			await openInDevContainer(result[0].fsPath);
		}
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.reopenInContainer', async () => {
		if (!await checkDockerVersion()) {
			return;
		}
		const folders = vscode.workspace.workspaceFolders || [];
		const folder = folders.length > 1 ? await vscode.window.showWorkspaceFolderPick({
			placeHolder: 'Pick a folder to reopen in a container.'
		}) : folders[0];
		if (folder && folder.uri.scheme === 'file') {
			await openInDevContainer(folder.uri.fsPath, { forceReuseWindow: true });
		}
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.reopenLocally', async () => {
		const folders = vscode.workspace.workspaceFolders;
		if (folders && folders[0] && folders[0].uri.scheme === 'vscode-remote') {
			const folder = getHostPathFromAuthority(folders[0].uri.authority);
			if (folder) {
				const uri = vscode.Uri.file(folder);
				const options: IOpenFolderAPICommandOptions = { forceReuseWindow: true };
				await vscode.commands.executeCommand('vscode.openFolder', uri, options);
			}
		}
	}));

	const attachCommand = async (arg: any) => {
		if (!await checkDockerVersion()) {
			return;
		}
		let containerInfo = undefined;
		// arg is not null when invoked fromt he Docker viewlet on a container node
		if (arg && arg.containerDesc) {
			containerInfo = arg.containerDesc;
		}
		attachToContainer(containerInfo);
	};

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.attachToRunningContainer', attachCommand));
	context.subscriptions.push(vscode.commands.registerCommand('openindocker.attachToRunningContainerFromViewlet', attachCommand));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.rebuildContainer', async () => {
		rebuild = true;
		return vscode.commands.executeCommand('workbench.action.reloadWindow');
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.createDevContainerFile', async () => {
		await createDevContainerFile();
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.openDevContainerFile', async () => {
		const devContainerConfigFiles = await getDevContainerFiles();

		if (devContainerConfigFiles && devContainerConfigFiles.length) {
			let file = devContainerConfigFiles.length === 1
				? devContainerConfigFiles[0]
				: await vscode.window.showQuickPick(devContainerConfigFiles, { placeHolder: 'Select container configuration file to open' });

			if (file) {
				if (await exists(file)) {
					return await vscode.commands.executeCommand('vscode.open', vscode.Uri.file(file));
				} else {
					return await createDevContainerFile();
				}
			}
		} else {
			return await createDevContainerFile();
		}
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.forwardPort', async () => {
		await forwardPortUI(resolverResults);
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.gitCredential', async (data: any) => {
		try {
			const { stdout, stderr } = await exec(`git credential ${data.command}`, {
				stdin: data.stdin,
				env: Object.assign(process.env, { GIT_TERMINAL_PROMPT: '0' })
			});
			return { stdout, stderr, code: 0 };
		} catch ({ stdout, stderr, error }) {
			const code = error.code || 0;
			if (stderr.indexOf('terminal prompts disabled') !== -1) {
				stderr = '';
			}
			return { stdout, stderr, code };
		}
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.settings', async () => {
		return vscode.commands.executeCommand('workbench.action.openSettings', `@ext:${getExtensionId()}`);
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.getHelp', async () => {
		return vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/containers'));
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.getStarted', async () => {
		return vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/containers/getting-started'));
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.reportIssue', async () => {
		return vscode.commands.executeCommand('vscode.openIssueReporter', getExtensionId());
	}));

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.provideFeedback', async () => {
		return vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/containers/provide-feedback'));
	}));

	context.subscriptions.push(vscode.languages.registerDocumentLinkProvider(
		[{ pattern: '**/.devcontainer/devcontainer.json', language: 'jsonc' }, { pattern: '**/.devcontainer.json', language: 'jsonc' }],
		new ConfigLinkProvider())
	);

	context.subscriptions.push(vscode.commands.registerCommand('openindocker.trySample', () => {
		trySample();
	}));

}

export async function deactivate(): Promise<void> {
	await Promise.all([...resolverResults.values()].map(result => Promise.all(result.params.shutdowns.map(shutdown => {
		try {
			shutdown(rebuild);
		} catch (err) {
			console.error(err);
		}
	}))));
}

function getExtensionId(): string {
	const { name, publisher } = require('../package.json') as { name: string, publisher: string };
	return `${publisher}.${name}`;
}

async function openInDevContainer(folderPath: string, options?: IOpenFolderAPICommandOptions) {
	const existingConfigPath = await findDevContainerConfigPath(folderPath);
	let containerConfig = existingConfigPath && await readDevContainerConfigFile(existingConfigPath);
	if (!containerConfig) {
		const newConfigPath = await createDevContainerConfigIn(folderPath);
		if (newConfigPath) {
			containerConfig = await readDevContainerConfigFile(newConfigPath);
		}
		if (!containerConfig) {
			return;
		}
	}

	let remotePath: string;
	if ('dockerFile' in containerConfig) {
		const dockerFilePath = getDockerfilePath(folderPath, containerConfig);
		if (!await exists(dockerFilePath)) {
			const result = await vscode.window.showErrorMessage(`"${dockerFilePath}" does not exist.`, { modal: true }, 'Open devcontainer.json');
			if (result) {
				vscode.window.showTextDocument(vscode.Uri.file(containerConfig.configFilePath));
			}
			return;
		}
		const { containerWorkspaceFolder } = await getDevContainerPaths(folderPath);
		remotePath = containerWorkspaceFolder;
	} else if ('image' in containerConfig) {
		const { containerWorkspaceFolder } = await getDevContainerPaths(folderPath);
		remotePath = containerWorkspaceFolder;
	} else if ('dockerComposeFile' in containerConfig) {
		if (!await checkDockerCompose() || !await canOpenDockerComposeDevContainer(folderPath, containerConfig)) {
			return;
		}
		remotePath = getWorkspaceFolder(containerConfig);
	} else {
		const result = await vscode.window.showErrorMessage(`Configuration "${path.relative(folderPath, containerConfig.configFilePath)}" not valid.`, { modal: true }, 'Open devcontainer.json');
		if (result) {
			vscode.window.showTextDocument(vscode.Uri.file(containerConfig.configFilePath));
		}
		return;
	}

	const uri = getDevContainerUri(folderPath, remotePath);
	await setRecentEntry({
		uri,
		type: 'folder',
		label: `${tildify(process.platform, folderPath, os.homedir())} [Dev Container]`
	});
	await vscode.commands.executeCommand('vscode.openFolder', uri, options);
}

function getWorkspaceFolder(config: DevContainerFromDockerComposeConfig) {
	return config.workspaceFolder || '/';
}

interface RecentEntry {
	uri: vscode.Uri;
	type: 'workspace' | 'folder' | 'file';
	label?: string;
}

async function setRecentEntry(entry: RecentEntry) {
	return vscode.commands.executeCommand('_workbench.addToRecentlyOpened', entry);
}

interface RunningContainerItem extends vscode.QuickPickItem {
	containerInfo: Docker.ContainerInfo;
}

async function attachToContainer(containerInfo?: Docker.ContainerInfo) {
	const docker = new Docker();

	if (!containerInfo) {
		containerInfo = await promptForContainer(docker);
	}
	if (!containerInfo) {
		return;
	}

	const options: INewWindowAPICommandOptions = {
		remoteAuthority: `attached-container+${Buffer.from(containerInfo.Id, 'utf8').toString('hex')}`,
		noRecentEntry: true
	};
	await vscode.commands.executeCommand('vscode.newWindow', options);
}

async function promptForContainer(docker: Docker): Promise<Docker.ContainerInfo | undefined> {
	const list = await docker.listContainers();
	let runningContainers: RunningContainerItem[] = [];

	list.forEach(each => {
		if (each.State === 'running') {
			runningContainers.push({ label: each.Names[0], description: `${each.Image} ${each.Id}`, containerInfo: each });
		}
	});
	if (runningContainers.length === 0) {
		vscode.window.showErrorMessage('There are no running containers to attach to.');
		return undefined;
	}
	let selection = await vscode.window.showQuickPick(runningContainers, { placeHolder: 'Select the container to attach VS Code' });
	if (!selection) {
		return undefined;
	}
	return selection.containerInfo;
}

async function canOpenDockerComposeDevContainer(workspacePath: string, containerConfig: DevContainerFromDockerComposeConfig) {
	const docker = new Docker();
	let container = await findTargetContainerFromLabel(docker, containerConfig.service);
	if (container && !await validateContainerInfo(workspacePath, containerConfig, container)) {
		return false;
	}
	return true;
}

async function validateContainerInfo(workspacePath: string, info: DevContainerFromDockerComposeConfig, container: Docker.ContainerInfo): Promise<boolean> {
	const composePaths = getDockerComposeFilePaths(workspacePath, info);
	const missingFiles = [];
	for (let i = 0; i < composePaths.length; i++) {
		if (!await exists(composePaths[i])) {
			missingFiles.push(composePaths[i]);
		}
	}
	if (missingFiles.length > 0) {
		vscode.window.showErrorMessage(`The following referenced docker compose files do not exist: ${missingFiles}`);
		return false;
	}
	if (info.devPort && !container.Ports.find((each) => info.devPort === each.PrivatePort)) {
		vscode.window.showErrorMessage(`The port '${info.devPort}' is not open on the service '${info.service}' `);
		return false;
	}
	return true;
}

async function findTargetContainerFromLabel(docker: Docker, serviceName: string): Promise<Docker.ContainerInfo | undefined> {
	const list = await docker.listContainers({ all: true, filters: { label: [`com.docker.compose.service=${serviceName}`] } });
	return list[0] && list[0];
}

async function getDevContainerFiles(): Promise<string[] | undefined> {
	const folders = vscode.workspace.workspaceFolders;
	if (folders) {
		const configs = await Promise.all(folders.map(async folder => {
			if (folder.uri.scheme !== 'file') {
				return undefined;
			}

			const folderPath = folder.uri.fsPath;

			return await findDevContainerConfigPath(folderPath);
		}));

		return configs.filter(config => !!config) as string[];
	}

	return Promise.resolve(undefined);
}

async function createDevContainerFile() {
	const folderPath = await createDevContainerConfig();
	if (folderPath) {
		await informUserAboutDevContainerIn(folderPath);
	}
}

async function informUserAboutDevContainer() {
	const folders = vscode.workspace.workspaceFolders;
	if (folders && folders.length === 1 && folders[0].uri.scheme === 'file') {
		const folderPath = folders[0].uri.fsPath;
		if (await findDevContainerConfigPath(folderPath)) {
			await informUserAboutDevContainerIn(folderPath);
		}
	}
}

async function informUserAboutDevContainerIn(folderPath: string) {
	const reopen = 'Reopen in Container';
	const result = await vscode.window.showInformationMessage(
		'Folder contains a dev container configuration file. Reopen folder to develop in a container ([learn more](https://aka.ms/vscode-remote/docker)).',
		reopen
	);
	if (result === reopen) {
		if (!await checkDockerVersion()) {
			return;
		}
		try {
			await openInDevContainer(folderPath, { forceReuseWindow: true });
		} catch (err) {
			await handleError(err);
		}
	}
}

async function trySample() {
	if (!await checkDockerVersion()) {
		return;
	}
	const samples: vscode.QuickPickItem[] = [
		{
			label: 'Go',
			detail: 'https://github.com/Microsoft/vscode-remote-try-go'
		},
		{
			label: 'Java',
			detail: 'https://github.com/Microsoft/vscode-remote-try-java'
		},
		{
			label: 'Node',
			detail: 'https://github.com/Microsoft/vscode-remote-try-node'
		},
		{
			label: 'Python',
			detail: 'https://github.com/Microsoft/vscode-remote-try-python'
		},
		{
			label: '.Net Core',
			detail: 'https://github.com/Microsoft/vscode-remote-try-dotnetcore'
		}
	];
	let options: vscode.QuickPickOptions = {
		placeHolder: 'Select the sample repository to open'
	};
	let repo = await vscode.window.showQuickPick(samples, options);
	if (!repo) {
		return;
	}
	let readme = `${repo.detail}/blob/master/README.md`;
	vscode.env.openExternal(vscode.Uri.parse(readme));
}