import * as fs from 'fs';
import { nfcall } from './async';

export function exists(path: string): Promise<boolean> {
	return new Promise(c => fs.exists(path, c));
}

export function readFile(file: string): Promise<Buffer> {
	return nfcall(fs.readFile, file);
}

export function mkdir(path: string): Promise<void> {
	return nfcall(fs.mkdir, path);
}

export function writeFile(path: string, contents: string): Promise<boolean> {
	return nfcall(fs.writeFile, path, contents);
}