import * as vscode from 'vscode';
import * as jsonc from 'jsonc-parser';
import { getDockerfilePath, DevContainerFromDockerfileConfig } from './singleContainer';
import { DevContainerFromDockerComposeConfig, getDockerComposeFilePaths } from './dockerCompose';

export class ConfigLinkProvider implements vscode.DocumentLinkProvider {

	provideDocumentLinks(document: vscode.TextDocument, _token: vscode.CancellationToken): vscode.ProviderResult<vscode.DocumentLink[]> {
		const result: vscode.DocumentLink[] = [];

		const syntaxTree = jsonc.parseTree(document.getText());
		const config: DevContainerFromDockerfileConfig | DevContainerFromDockerComposeConfig = jsonc.getNodeValue(syntaxTree);
		config.configFilePath = document.uri.fsPath;

		if ('dockerFile' in config) {
			this._linksForDockerfile(result, config, syntaxTree, document);
		}
		if ('dockerComposeFile' in config) {
			this._linksForDockerCompose(result, config, syntaxTree, document);
		}
		return result;
	}

	private _linksForDockerfile(bucket: vscode.DocumentLink[], config: DevContainerFromDockerfileConfig, syntaxTree: jsonc.Node, document: vscode.TextDocument) {
		const path = getDockerfilePath('', config);
		const node = jsonc.findNodeAtLocation(syntaxTree, ['dockerFile']);
		if (node && node.type === 'string') {
			bucket.push(new vscode.DocumentLink(
				new vscode.Range(document.positionAt(node.offset + 1), document.positionAt(node.offset + node.length - 1)),
				document.uri.with({ path })
			));
		}
	}

	private _linksForDockerCompose(bucket: vscode.DocumentLink[], config: DevContainerFromDockerComposeConfig, syntaxTree: jsonc.Node, document: vscode.TextDocument) {
		const paths = getDockerComposeFilePaths('', config);
		const node = jsonc.findNodeAtLocation(syntaxTree, ['dockerComposeFile']);
		if (paths.length > 0 && node) {
			if (node.type === 'array' && node.children) {
				for (let i = 0; i < node.children.length; i++) {
					bucket.push(new vscode.DocumentLink(
						new vscode.Range(document.positionAt(node.children[i].offset + 1), document.positionAt(node.children[i].offset + node.children[i].length - 1)),
						document.uri.with({ path: paths[i] })
					));
				}
			} else if (node.type === 'string') {
				bucket.push(new vscode.DocumentLink(
					new vscode.Range(document.positionAt(node.offset + 1), document.positionAt(node.offset + node.length - 1)),
					document.uri.with({ path: paths[0] })
				));
			}
		}
	}
}
