import * as net from 'net';
import { findWindowsExecutable } from './resolverUtils';
import * as cp from 'child_process';

const debugOutput = false;

export interface ForwardedPort {
	containerId: string;
	targetPort: number;
	server: net.Server;
	connections: Set<net.Socket>;
}

export async function forwardPort(forwardedPorts: Set<ForwardedPort>, cwd: string, containerId: string, targetPort: number, nodePath: string) {
	if (debugOutput) {
		console.error(`Forward port ${targetPort}: Start listening`);
	}
	return new Promise<net.Server>((resolve, reject) => {
		const connections = new Set<net.Socket>();
		const server = net.createServer({ pauseOnConnect: true }, socket => {
			if (debugOutput) {
				console.error(`Forward port ${targetPort}: New connection`);
			}
			dockerExec(cwd, containerId, [nodePath, '-e', `
				const net = require('net');
				process.stdin.pause();
				const client = net.createConnection({ port: ${targetPort} }, () => {
					client.pipe(process.stdout);
					process.stdin.pipe(client);
				});
				client.on('close', function (hadError) {
${debugOutput ? `					console.error('Forward port ' + ${targetPort} + ': Remote close');
` : ''}					process.exit(hadError ? 1 : 0);
				});
				client.on('error', function (err) {
					process.stderr.write(err && (err.stack || err.message) || String(err));
				});
			`])
				.then(child => {
					let killed = false;
					child.on('exit', (code, signal) => {
						if (debugOutput) {
							console.error(`Forward port ${targetPort}: Remote exit`);
						}
						if (!killed) {
							connections.delete(socket);
							socket.end();
							if (code || signal) {
								console.error(`Port forwarding terminated with code ${code} and signal ${signal}.`);
							}
						}
					});
					child.stderr.on('data', (chunk: Buffer) => {
						console.error(chunk.toString('utf8'));
					});
					socket.on('close', () => {
						if (debugOutput) {
							console.error(`Forward port ${targetPort}: Local close`);
						}
						killed = true;
						connections.delete(socket);
						child.kill('SIGKILL');
					});
					child.stdout.pipe(socket);
					socket.pipe(child.stdin);

					connections.add(socket);
				}).catch(console.error);
		});
		server.on('listening', () => {
			const forwardedPort = {
				containerId,
				targetPort,
				server,
				connections,
			};
			server.on('close', () => {
				forwardedPorts.delete(forwardedPort);
			});
			forwardedPorts.add(forwardedPort);
			resolve(server);
		});
		server.on('error', err => {
			if (err && (<any>err).code === 'EADDRINUSE') {
				server.listen(0, '127.0.0.1');
				return;
			}
			reject(err);
		});
		server.on('close', () => {
			reject(new Error('Closed'));
		});
		server.listen(targetPort, '127.0.0.1');
	});
}

export async function dockerExec(cwd: string, containerId: string, cmd: string[]) {
	const docker = await findWindowsExecutable('docker', cwd, process.env);
	return cp.spawn(docker, ['exec', '-i', containerId, ...cmd], { cwd });
}
