import * as vscode from 'vscode';
import * as path from 'path';
import * as fs from 'fs';
import * as mkdirp from 'mkdirp';
import * as yaml from 'js-yaml';
import * as MarkdownIt from 'markdown-it';

import { exists, writeFile } from './pfs';
import { readDevContainerConfigFile, DevContainerConfig } from './containerResolver';
import { promisify } from 'util';
import { getDevContainerConfigPathIn, getDefaultDevContainerConfigPath } from './resolverUtils';
import { getDockerfilePath } from './singleContainer';
import { getDockerComposeFilePaths } from './dockerCompose';

export async function createDevContainerConfig() {
	const folders = vscode.workspace.workspaceFolders || [];
	const folder = folders.length > 1 ? await vscode.window.showWorkspaceFolderPick({
		placeHolder: 'Select a folder to create a container configuration for'
	}) : folders[0];
	if (folder && folder.uri.scheme === 'file') {
		const folderPath = folder.uri.fsPath;
		const containerConfigPath = await createDevContainerConfigIn(folderPath);
		if (containerConfigPath) {
			const document = await vscode.workspace.openTextDocument(containerConfigPath);
			vscode.window.showTextDocument(document);
			return folderPath;
		}
	}
	return undefined;
}

interface DockerfileConfiguration {
	config: {
		dockerFile: string;
		appPort?: number;
		extensions?: string[];
	};
}

interface DockerComposeConfiguration {
	config: {
		dockerComposeFile: string;
		service: string;
		workspaceFolder: '/';
		appPort?: number;
		extensions?: string[];
	};
}

interface BaseConfiguration {
	label: string;
	type: 'baseConfiguration';
	description?: string;
	definitionFolder: string;
	config: DevContainerConfig | { configFilePath: string; name?: string; extensions?: string; };
}

interface DockerFileItem {
	label: string;
	dockerfile: string;
	dockerfilePath: string;
	type: 'dockerfile';
}

interface DockerComposeFileItem {
	label: string;
	dockerComposeFile: string;
	dockerComposePath: string;
	type: 'dockerComposeFile';
}

interface ShowHelpItem {
	type: 'help';
	label: string;
	description: string;
	alwaysShow: boolean;
}

interface ShowPredefinedItem {
	type: 'predefined';
	detail: string;
	label: string;
	alwaysShow: boolean;
}

interface ServiceItem {
	type: 'service';
	label: string;
}

type ContainerQuickPickItem = BaseConfiguration | DockerFileItem | DockerComposeFileItem | ShowHelpItem | ShowPredefinedItem | ServiceItem;

function showContainerConfigurationQuickPick(items: ContainerQuickPickItem[], placeholder: string): Promise<ContainerQuickPickItem | undefined> {
	const quickPick = vscode.window.createQuickPick<ContainerQuickPickItem>();
	quickPick.title = 'Create Development Container Configuration';
	quickPick.placeholder = placeholder;
	quickPick.items = items;
	const toDispose: vscode.Disposable[] = [];

	const promise = new Promise<ContainerQuickPickItem | undefined>((c) => {
		toDispose.push(quickPick.onDidAccept(() => {
			toDispose.forEach(d => d.dispose());
			c(quickPick.selectedItems[0]);
		}));
		toDispose.push(quickPick.onDidHide(() => {
			toDispose.forEach(d => d.dispose());
			c(undefined);
		}));
	});

	quickPick.show();
	return promise;
}

export async function createDevContainerConfigIn(folderPath: string): Promise<string | undefined> {
	const existingConfigPath = await getDevContainerConfigPathIn(folderPath);
	if (existingConfigPath) {
		vscode.window.showErrorMessage(`Dev Container configuration '${path.relative(folderPath, existingConfigPath)}' file already exists.`, { modal: true });
		const document = await vscode.workspace.openTextDocument(existingConfigPath);
		vscode.window.showTextDocument(document);
		return undefined;
	}

	const newConfigPath = getDefaultDevContainerConfigPath(folderPath);
	let baseConfig: BaseConfiguration | DockerfileConfiguration | DockerComposeConfiguration | undefined;

	const dockerComposeFiles = await vscode.workspace.findFiles('*docker*compose*');
	const dockerfiles = await vscode.workspace.findFiles('*{D,d}ockerfile*');
	const dockerfileItems: DockerFileItem[] = dockerfiles.map(dockerfile => {
		const basename = path.basename(dockerfile.fsPath);
		return {
			label: `From '${basename}'`,
			dockerfile: basename,
			dockerfilePath: dockerfile.fsPath,
			detail: `Refer to this file in the container configuration`,
			type: 'dockerfile'
		};
	});

	const dockerComposeItems: DockerComposeFileItem[] = dockerComposeFiles.map(dockerComposeFile => {
		const basename = path.basename(dockerComposeFile.fsPath);
		return {
			label: `From '${basename}'`,
			dockerComposeFile: basename,
			dockerComposePath: dockerComposeFile.fsPath,
			detail: `Refer to this file in the container configuration`,
			type: 'dockerComposeFile'
		};
	});

	let pick: ContainerQuickPickItem | undefined = undefined;

	let predefined: ContainerQuickPickItem = {
		label: 'From a predefined container configuration definition...',
		detail: 'Use a base configuration from the container definition registry',
		alwaysShow: true,
		type: 'predefined'
	};

	if (dockerfiles.length === 0 && dockerComposeFiles.length === 0) {
		pick = predefined;
	} else {
		pick = await showContainerConfigurationQuickPick([
			predefined,
			...dockerfileItems,
			...dockerComposeItems,
			{
				label: 'Learn More',
				description: 'Documentation on how to set up a folder to run in a container',
				alwaysShow: true,
				type: 'help'
			}
		], 'How would you like to create your container configuration?');
	}


	if (!pick) {
		return Promise.resolve(undefined);
	}

	if (pick.type === 'help') {
		vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/containers/folder-setup'));
	}

	if (pick.type === 'predefined') {
		const configs = await getBaseConfigurations();
		const pick = await showContainerConfigurationQuickPick([
			{
				label: 'Learn More',
				description: 'Documentation on predefined container definitions',
				alwaysShow: true,
				type: 'help'
			},
			...configs
		], 'Select a container configuration definition');

		if (!pick) {
			return Promise.resolve(undefined);
		}
		if (pick.type === 'help') {
			vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-dev-containers'));
		}
		if (pick.type === 'baseConfiguration') {
			baseConfig = pick;
		}
	}

	if (pick.type === 'dockerComposeFile') {
		const text = await promisify(fs.readFile)(pick.dockerComposePath, { encoding: 'utf8' });
		const comp = yaml.safeLoad(text);
		const services = Object.keys(comp && comp.services || {});
		const service = services.length < 2 ? services[0] || '' : await showContainerConfigurationQuickPick(services.map(s => ({ label: s, type: 'service' })), 'Select a service');
		if (service === undefined) {
			return undefined;
		}
		baseConfig = {
			config: {
				dockerComposeFile: path.relative(path.dirname(newConfigPath), pick.dockerComposePath),
				service: typeof service === 'string' ? service : service.label,
				workspaceFolder: '/',
				extensions: []
			}
		};
	}

	if (pick.type === 'dockerfile') {
		baseConfig = {
			config: {
				dockerFile: path.relative(path.dirname(newConfigPath), pick.dockerfilePath),
				extensions: []
			}
		};
	}

	if (!baseConfig) {
		return Promise.resolve(undefined);
	}

	if ('definitionFolder' in baseConfig) {
		return copyBaseConfiguration(folderPath, baseConfig, undefined);
	} else {
		await promisify(mkdirp)(path.dirname(newConfigPath));
		await writeFile(newConfigPath, getDevContainerConfigTemplate(baseConfig, undefined));
		return newConfigPath;
	}
}

let baseConfigurations: Promise<(BaseConfiguration)[]> | undefined = undefined;

async function getBaseConfigurations(): Promise<BaseConfiguration[]> {
	if (baseConfigurations === undefined) {
		baseConfigurations = getBaseRepoConfigurations().then(configs => {
			return configs
				.sort((a, b) => a.label.localeCompare(b.label));
		});
	}
	return baseConfigurations;
}

async function getBaseRepoConfigurations(): Promise<BaseConfiguration[]> {
	const md = new MarkdownIt();
	const basePath = path.join(__dirname, 'node_modules', 'vscode-dev-containers', 'containers');
	const readmesBasePath = path.join(__dirname, 'node_modules', 'vscode-dev-containers', 'containers-readmes');
	const entries = await promisify(fs.readdir)(basePath);
	const configs = await Promise.all(entries.map(async entry => {
		const definitionFolder = path.join(basePath, entry);
		const configPath = await getDevContainerConfigPathIn(definitionFolder);
		const config = configPath && await readDevContainerConfigFile(configPath);
		if (!config) {
			return undefined;
		}
		const label = config.name || entry;
		let description: string | undefined;
		if ('image' in config) {
			description = config.image;
		} else if ('dockerFile' in config) {
			const dockerfilePath = getDockerfilePath(definitionFolder, config);
			if (await exists(dockerfilePath)) {
				const content = await promisify(fs.readFile)(dockerfilePath, { encoding: 'utf8' });
				description = (/^FROM\s+(\S+)/m.exec(content) || [])[1];
			}
		} else if ('dockerComposeFile' in config) {
			const dockerComposePath = getDockerComposeFilePaths(definitionFolder, config)[0];
			if (dockerComposePath && await exists(dockerComposePath)) {
				try {
					const text = await promisify(fs.readFile)(dockerComposePath, { encoding: 'utf8' });
					const comp = yaml.safeLoad(text);
					const services = comp && comp.services;
					const service = services && services[config.service];
					const build = service && service.build;
					const image = build && build.image;
					if (image) {
						description = image;
					}
					const dockerfile = build && build.dockerfile;
					const dockerfilePath = dockerfile && path.resolve(path.dirname(dockerComposePath), dockerfile);
					if (await exists(dockerfilePath)) {
						const content = await promisify(fs.readFile)(dockerfilePath, { encoding: 'utf8' });
						description = (/^FROM\s+(\S+)/m.exec(content) || [])[1];
					}
				} catch (err) {
					console.error(err);
				}
			}
		}
		let detail: string | undefined;
		const readme = path.join(readmesBasePath, entry, 'README.md');
		if (await exists(readme)) {
			try {
				const content = await promisify(fs.readFile)(readme, { encoding: 'utf8' });
				const match = /\#\s*summary.*\n*(.*)/i.exec(content);
				if (match) {
					const result = md.render(match[1]);
					detail = result.replace(/<[^>]*>/g, '').trim();
					const i = detail.indexOf('. ');
					if (i !== -1) {
						detail = detail.substr(0, i + 1);
					}
				}
			} catch (err) {
				console.error(err);
			}
		}
		return {
			label,
			description,
			type: 'baseConfiguration',
			detail,
			definitionFolder,
			config
		} as BaseConfiguration;
	}));
	return configs.filter(config => !!config) as BaseConfiguration[];
}

async function copyBaseConfiguration(folderPath: string, config: BaseConfiguration, port: number | undefined) {
	const updatePort = port !== (<any>config.config).appPort;
	const skip = 'Skip';
	const relativeFiles = (await recursiveFilePaths(config.definitionFolder))
		.map(absolutePath => path.relative(config.definitionFolder, absolutePath));
	let destConfigFilePath: string | undefined;
	for (const relativeFile of relativeFiles) {
		const dest = path.join(folderPath, relativeFile);
		if (await exists(dest)) {
			const res = await vscode.window.showInformationMessage(`File ${relativeFile} already exists, overwrite?`, { modal: true }, skip, 'Overwrite');
			if (!res) {
				return undefined;
			}
			if (res === skip) {
				continue;
			}
		}
		await promisify(mkdirp)(path.dirname(dest));
		const src = path.join(config.definitionFolder, relativeFile);
		const isConfigFile = src === config.config.configFilePath;
		if (isConfigFile) {
			destConfigFilePath = dest;
		}
		if ((updatePort || true) && isConfigFile) {
			await writeFile(dest, getDevContainerConfigTemplate(config, port));
		} else {
			await promisify(fs.copyFile)(src, dest);
		}
	}
	return destConfigFilePath;
}

async function recursiveFilePaths(folder: string): Promise<string[]> {
	const entries = await promisify(fs.readdir)(folder);
	return ([] as string[]).concat(...await Promise.all(entries.map(async entry => {
		const entryPath = path.join(folder, entry);
		if ((await promisify(fs.lstat)(entryPath)).isDirectory()) {
			return recursiveFilePaths(entryPath);
		}
		return entryPath;
	})));
}

function getDevContainerConfigTemplate({ config }: BaseConfiguration | DockerfileConfiguration | DockerComposeConfiguration, port: number | undefined): string {
	config = Object.assign({}, config);
	delete (<any>config).configFilePath;
	if (typeof port === 'number') {
		(<any>config).appPort = port; // TODO: any
		// } else {
		// 	delete (<any>config).appPort;
	}

	const header = '// See https://aka.ms/vscode-remote/containers for the\n// documentation about the devcontainer.json format';
	return `${header}\n${JSON.stringify(config, null, '\t')}`;
}
