import * as cp from 'child_process';
import { findWindowsExecutable } from "./resolverUtils";
import { promisify } from "util";

export async function findListeningPorts(cwd: string, containerId: string) {
	const connections = await loadListeningPorts(cwd, containerId);
	const sockets = await getSockets(cwd, containerId);
	const socketMap = sockets.reduce((m, socket) => {
		m[socket.socket] = socket;
		return m;
	}, {} as Record<string, typeof sockets[0]>);
	const processes = await findProcesses(cwd, containerId);
	const processMap = processes.reduce((m, process) => {
		m[process.pid] = process;
		return m;
	}, {} as Record<string, typeof processes[0]>);
	return connections.filter((connection => socketMap[connection.socket])).map(({ socket, ip, port }) => ({
		socket,
		ip,
		port,
		process: processMap[socketMap[socket].pid]
	}));
}

async function findProcesses(cwd: string, containerId: string) {
	const ps = 'for pid in `cd /proc && ls -d [0-9]*`; do { echo $pid ; readlink -f /proc/$pid/cwd ; xargs -0 < /proc/$pid/cmdline ; } | tr "\n" " " ; echo ; done 2>/dev/null';
	const { stdout } = await execAsRoot(cwd, containerId, ps);

	const n = 3;
	const processes = stdout.split('\n')
		.map(line => line.split(' '))
		.filter(parts => parts.length >= n)
		.map(parts => [...parts.slice(0, n - 1), parts.slice(n - 1).join(' ')])
		.map(parts => ({
			pid: parseInt(parts[0], 10),
			cwd: parts[1],
			cmd: parts[2]
		}));
	return processes;
}

async function getSockets(cwd: string, containerId: string) {
	const { stdout } = await execAsRoot(cwd, containerId, 'ls -l /proc/[0-9]*/fd/[0-9]* 2>/dev/null | grep socket:');
	const lines = stdout.trim().split('\n');
	return lines.map(line => {
		const match = /\/proc\/(\d+)\/fd\/\d+ -> socket:\[(\d+)\]/.exec(line)!;
		return {
			pid: parseInt(match[1], 10),
			socket: parseInt(match[2], 10)
		};
	});
}

async function loadListeningPorts(cwd: string, containerId: string) {
	const table = [
		...await loadConnectionTable(cwd, containerId, '/proc/net/tcp'),
		...await loadConnectionTable(cwd, containerId, '/proc/net/tcp6')
	];
	return table.filter(row => row.st === '0A')
		.map(row => {
			const address = row.local_address.split(':');
			return {
				socket: parseInt(row.inode, 10),
				ip: address[0],
				port: parseInt(address[1], 16)
			};
		});
}

async function loadConnectionTable(cwd: string, containerId: string, filePath: string) {
	const { stdout } = await execAsRoot(cwd, containerId, `cat ${filePath}`);
	const lines = stdout.trim().split('\n');
	const names = lines.shift()!.trim().split(/\s+/)
		.filter(name => name !== 'rx_queue' && name !== 'tm->when');
	const table = lines.map(line => line.trim().split(/\s+/).reduce((obj, value, i) => {
		obj[names[i] || i] = value;
		return obj;
	}, {} as Record<string, string>));
	return table;
}

async function execAsRoot(cwd: string, containerId: string, cmd: string) {
	const docker = await findWindowsExecutable('docker', cwd, process.env);
	return promisify(cp.execFile)(docker, ['exec', '-u', '0', containerId, '/bin/sh', '-c', cmd], { cwd });
}
