import * as crypto from 'crypto';
import * as path from 'path';
import * as vscode from 'vscode';
import * as Docker from 'dockerode';
import * as os from 'os';

import { exists } from './pfs';
import * as ports from './ports';
import { showProgress, runCommand, startEventSeen, defaultDevPort, containerRemoteContext, ResolverParameters, inspectImage, printPortInfo, getConfigFilePath, getDevContainerPaths, eventOrStatus } from './resolverUtils';
import { getInstallLocation, installVSCHeadless, launchVSCHeadless, getNodeLocation, getHomedirFromContainer, getHomedirFromImage, findExtensionHostProcesses, runPostCreateCommand } from './injectHeadless';
import { forwardPort } from './portForwarding';
import { promisify } from 'util';
import { Server } from 'net';
import { getProduct } from './product';
import { bailOut, ContainerError } from './errors';

const qualityLabel = 'vsch.quality';
const hostFolderLabel = 'vsch.local.folder';
const hostDevPortLabel = 'vsch.local.devPort';
const containerDevPortLabel = 'vsch.remote.devPort';

export interface DevContainerFromImageConfig {
	configFilePath: string;
	image: string;
	name?: string;
	appPort?: number | string | (number | string)[];
	devPort?: number;
	extensions?: string[];
	runArgs?: string[];
	shutdownAction?: 'none' | 'stopContainer';
	overrideCommand?: boolean;
	postCreateCommand?: string | string[];
}

export interface DevContainerFromDockerfileConfig {
	configFilePath: string;
	dockerFile: string;
	context?: string;
	name?: string;
	appPort?: number | string | (number | string)[];
	devPort?: number;
	extensions?: string[];
	runArgs?: string[];
	shutdownAction?: 'none' | 'stopContainer';
	overrideCommand?: boolean;
	postCreateCommand?: string | string[];
}

export async function openDockerfileDevContainer(params: ResolverParameters, config: DevContainerFromDockerfileConfig | DevContainerFromImageConfig): Promise<vscode.ResolvedAuthority> {

	let container: Docker.ContainerInfo | undefined;
	try {

		container = await findDevContainer(params.docker, params.cwd);

		let containerWorkspaceFolder: string;
		if (container) {
			const start = container.State === 'exited';
			if (start) {
				await params.docker.getContainer(container.Id)
					.start();
				container = await findDevContainer(params.docker, params.cwd);
				if (!container) {
					return bailOut(params.output, 'Dev container not found.');
				}
			}
			containerWorkspaceFolder = (await getDevContainerPaths(params.cwd)).containerWorkspaceFolder;
		} else {

			const folderHash = getFolderHash(params.cwd);
			const baseName = path.basename(params.cwd).replace(/\s/g, '').toLowerCase();
			const folderImageName = `${baseName}-${folderHash}`;
			const imageName = 'image' in config ? config.image : `vsc-${folderImageName}`;

			if ('dockerFile' in config) {
				try {
					const { result, pulse } = await buildImage(params, config, imageName);
					await showProgress(params, `Building an image from the ${config.dockerFile}.`, result, pulse);
				} catch (err) {
					throw err instanceof ContainerError ? err : new ContainerError({ description: 'An error occurred building the image.', originalError: err });
				}
			}

			try {
				containerWorkspaceFolder = (await spawnDevContainer(params, config, imageName)).containerWorkspaceFolder;
			} finally {
				// In 'finally' because 'docker run' can fail after creating the container.
				// Trying to get it here, so we can offer 'Rebuild Container' as an action later.
				container = await findDevContainer(params.docker, params.cwd);
			}
			if (!container) {
				return bailOut(params.output, 'Dev container not found.');
			}
		}
		params.containerId = container.Id;

		const homedir = await getHomedirFromContainer(params.docker, container.Id);
		const installLocation = await getInstallLocation(homedir);
		const nodeLocation = await getNodeLocation(homedir);

		const labels = container.Labels;
		let hostPort = parseInt(labels[hostDevPortLabel], 10);
		const containerPort = parseInt(labels[containerDevPortLabel], 10);

		await installVSCHeadless(params, container.Id, installLocation, homedir);
		await runPostCreateCommand(params, container.Id, homedir, config.postCreateCommand, containerWorkspaceFolder);
		await launchVSCHeadless(params, container.Id, installLocation, homedir, containerPort);

		let hostPortServer: Server;
		if (!hostPort) {
			hostPortServer = await forwardPort(params.forwardedPorts, params.cwd, container.Id, containerPort, nodeLocation);
			hostPort = hostPortServer.address().port;
		}

		printPortInfo(params.output, hostPort, container);

		const cont = await params.docker.getContainer(container.Id);
		const shutdown = async (rebuild?: boolean) => {
			vscode.commands.executeCommand('setContext', containerRemoteContext, false);
			if (rebuild) {
				const removed = eventOrStatus(params.docker, cont.id, 'die');
				await cont.remove({ force: true });
				await removed;
			} else if (!config.shutdownAction || config.shutdownAction === 'stopContainer') {
				const forwardedDevPort = [...params.forwardedPorts].find(p => p.targetPort === containerPort);
				const devConnectionCount = forwardedDevPort ? forwardedDevPort.connections.size : 0;
				const extensionHostsIfLast = devConnectionCount <= 1 ? 0 : 1; // TODO: Better detection...
				if ((await findExtensionHostProcesses(params.cwd, cont.id)).length <= extensionHostsIfLast) {
					const stopped = eventOrStatus(params.docker, cont.id, 'kill');
					await cont.stop({ t: 0 });
					await stopped;
				}
			}
			if (hostPortServer) {
				await promisify(hostPortServer.close.bind(hostPortServer))();
			}
		};

		vscode.commands.executeCommand('setContext', containerRemoteContext, true);
		params.shutdowns.push(shutdown);
		return new vscode.ResolvedAuthority('localhost', hostPort);

	} catch (err) {
		if (err instanceof ContainerError) {
			err.manageContainer = !!container;
			err.container = container;
			throw err;
		}
		throw new ContainerError({
			description: 'An error occurred setting up the container.',
			originalError: err,
			manageContainer: !!container,
			container
		});
	}
}

export function getDockerfilePath(workspaceFolderPath: string, config: DevContainerFromDockerfileConfig) {
	return getConfigFilePath(workspaceFolderPath, config, config.dockerFile);
}

export function getDockerContextPath(workspaceFolderPath: string, config: DevContainerFromDockerfileConfig) {
	if (config.context) {
		return getConfigFilePath(workspaceFolderPath, config, config.context);
	}
	return path.dirname(getDockerfilePath(workspaceFolderPath, config));
}

async function findDevContainer(docker: Docker, folderPath: string): Promise<Docker.ContainerInfo | undefined> {
	const product = await getProduct();
	const list = await docker.listContainers({
		all: true,
		filters: {
			label: [
				`${qualityLabel}=${product.quality}`,
				`${hostFolderLabel}=${folderPath}`,
			]
		}
	});
	return list[0];
}

function getFolderHash(fsPath: string): string {
	return crypto.createHash('md5').update(fsPath).digest('hex');
}

async function buildImage(params: ResolverParameters, config: DevContainerFromDockerfileConfig, baseImageName: string) {
	const dockerfilePath = getDockerfilePath(params.cwd, config);
	if (!(await exists(dockerfilePath))) {
		throw new ContainerError({ description: `Dockerfile (${dockerfilePath}) not found.` });
	}
	const args = ['build', '-f', dockerfilePath, '-t', baseImageName, getDockerContextPath(params.cwd, config)];
	return runCommand(params.cwd, 'docker', args, params.output);
}

async function spawnDevContainer(params: ResolverParameters, config: DevContainerFromDockerfileConfig | DevContainerFromImageConfig, imageName: string) {

	const appPort = config.appPort;
	const exposedPorts = typeof appPort === 'number' || typeof appPort === 'string' ? [appPort] : appPort || [];
	const exposed = (<string[]>[]).concat(...exposedPorts.map(port => ['-p', typeof port === 'number' ? `127.0.0.1:${port}:${port}` : port]));

	const info = await inspectImage(params, imageName);
	const homeContainerPath = await getHomedirFromImage(info);

	const nestedInfo = os.platform() === 'linux' && process.env.HOSTNAME && await exists('/.dockerenv')
		? await params.docker.getContainer(process.env.HOSTNAME)
			.inspect()
		: undefined;

	let devArgs: string[] = [];
	let devLn = '';
	const product = await getProduct();
	if (!product.commit) {
		const installLocation = await getInstallLocation(homeContainerPath);
		const vscodeRepoPath = vscode.env.appRoot;
		devArgs = ['-v', `${vscodeRepoPath}:${installLocation}:ro`];
		if (nestedInfo) {
			const mount = findMount(nestedInfo, params.cwd);
			if (mount && (<any>mount).Type === 'volume') {
				devArgs = ['-v', `${(<any>mount).Name}:${installLocation}-volume:ro`];
				const relative = vscodeRepoPath.substr(mount.Destination.length);
				devLn = `ln -s '${path.join(`${installLocation}-volume`, relative)}' ${installLocation} ; `;
			}
		}
	}

	const containerPort = config.devPort || defaultDevPort;
	let devPorts: string[] = [];
	if (config.devPort) {
		const hostPort = await findFreePort();
		devPorts = [
			'-p', `127.0.0.1:${hostPort}:${containerPort}`,
			'-l', `${hostDevPortLabel}=${hostPort}`,
		];
	}

	const paths = await getDevContainerPaths(params.cwd);
	let cwdMount = ['-v', `${paths.hostMountFolder}:${paths.containerMountFolder}`];
	let cwdLn = '';
	if (nestedInfo) {
		const mount = findMount(nestedInfo, paths.hostMountFolder);
		if (mount && (<any>mount).Type === 'volume') {
			cwdMount = ['-v', `${(<any>mount).Name}:${paths.containerMountFolder}-volume`];
			const relative = paths.hostMountFolder.substr(mount.Destination.length);
			cwdLn = `ln -s '${path.join(`${paths.containerMountFolder}-volume`, relative)}' ${paths.containerMountFolder} ; `;
		}
	}

	const cmd = config.overrideCommand !== false ? ['/bin/sh', '-c', `echo Container started ; ${cwdLn}${devLn} while sleep 1; do :; done`] : [];

	const args = [
		'run',
		'-a', 'STDOUT',
		'-a', 'STDERR',
		...devPorts,
		...exposed,
		...cwdMount,
		...await getGitMounts(homeContainerPath, nestedInfo),
		...devArgs,
		'-l', `${qualityLabel}=${product.quality}`,
		'-l', `${hostFolderLabel}=${params.cwd}`,
		'-l', `${containerDevPortLabel}=${containerPort}`,
		...(config.runArgs || []),
		imageName,
		...cmd
	];

	let cancel: () => void;
	const canceled = new Promise<void>((_, reject) => cancel = reject);
	const { started } = await startEventSeen(params.docker, hostFolderLabel, params.cwd, canceled);

	const { result, pulse } = await runCommand(params.cwd, 'docker', args, params.output);
	result.then(cancel!, cancel!);

	await showProgress(params, 'Starting the development container', started, pulse);
	return paths;
}

function findMount(info: Docker.ContainerInspectInfo, folder: string) {
	return info.Mounts.reduce<typeof info.Mounts[0] | undefined>((best, current) => {
		const relative = path.relative(current.Destination, folder);
		const isParent = !relative || !relative.startsWith('..') && !path.isAbsolute(relative);
		if (isParent && (!best || current.Destination.length > best.Destination.length)) {
			best = current;
		}
		return best;
	}, undefined);
}

async function getGitMounts(homeContainerPath: string, nestedInfo?: Docker.ContainerInspectInfo | undefined) {
	const isWindows = os.platform() === 'win32';
	const homeHostPath = isWindows ? process.env.USERPROFILE : process.env.HOME;
	if (!homeHostPath) {
		return [];
	}
	let gitConfigHostPath = path.join(homeHostPath, '.gitconfig');
	if (!await exists(gitConfigHostPath)) {
		return [];
	}
	if (nestedInfo) {
		const mount = findMount(nestedInfo, gitConfigHostPath);
		if (mount && (<any>mount).Type === 'bind') {
			gitConfigHostPath = mount.Source;
		}
	}
	const gitConfigContainerPath = path.posix.join(homeContainerPath, '.gitconfig');
	return ['-v', `${gitConfigHostPath}:${gitConfigContainerPath}`];
}

async function findFreePort(): Promise<number> {
	return ports.findFreePort(ports.randomPort(), 10, 3000);
}
