export const extensionHostReadyExp = /Extension host agent listening on \d+/;

export interface ResolverParameters {
	docker: Docker;
	authority: string;
	cwd: string;
	progress: vscode.Progress<{ message?: string; increment?: number }>;
	output: vscode.TerminalRenderer;
	shutdowns: ((rebuild?: boolean) => Promise<void>)[];
	forwardedPorts: Set<ForwardedPort>;
	containerId?: string;
}

export interface ResolverResult {
	authority: string;
	params: ResolverParameters;
	resolvedAuthority: vscode.ResolvedAuthority;
}

export function resolver(resolvedAuthorities: Map<string, ResolverResult>, resolve: (authority: string) => Promise<ResolverResult>) {
	return async (authority: string, context: vscode.RemoteAuthorityResolverContext): Promise<vscode.ResolvedAuthority> => {
		if (context.resolveAttempt > 1) {
			const result = resolvedAuthorities.get(authority);
			if (!result) {
				throw new Error('Reconnect attempt without old result.');
			}
			return result.resolvedAuthority;
		} else {
			const result = await resolve(authority);
			resolvedAuthorities.set(authority, result);
			return result.resolvedAuthority;
		}
	};
}

