export async function runCommandInDetachedProcess(cwd: string, cmd: string, args: string[], options: { env?: Record<string, string>, resolveOn?: RegExp } = {}) {
	const env = { ...process.env as any, ...(options.env || {}) };
	cmd = await findWindowsExecutable(cmd, cwd, env);

	if (process.platform === 'win32') {
		const child = cp.spawn('START', ['/B', '/min' /** avoid popup */, `""` /** title */, `"${cmd}"`/** cmd might contain spaces */, ...args], { shell: true, detached: true, stdio: 'ignore' });
		child.unref();
	} else {
		args.push('&');
		const child = cp.spawn(cmd, args, { detached: true, stdio: 'ignore' });
		child.unref();
	}
}

export async function runCommand(cwd: string, cmd: string, args: string[], outputChannel: vscode.TerminalRenderer, options: { env?: Record<string, string>, resolveOn?: RegExp } = {}) {
	outputChannel.write(`Run: ${cmd} ${args.join(' ')}\r\n`);

	const env = { ...process.env as any, ...(options.env || {}) };
	cmd = await findWindowsExecutable(cmd, cwd, env);
	const pulse = new vscode.EventEmitter<void>();
	const result = new Promise<{ cmdOutput: string }>((resolve, reject) => {
		let output = '';

		const pty = getNodeModule<typeof ptyType>('node-pty');

		if (!pty) {
			reject(new Error('Missing node-pty'));
			return;
		}

		const experimentalUseConpty = false; // TODO: Investigate using a shell with ConPTY. https://github.com/Microsoft/vscode-remote/issues/1234#issuecomment-485501275
		const p = pty.spawn(cmd, args, {
			cwd,
			env,
			cols: 999,
			rows: 100,
			experimentalUseConpty,
		});

		p.on('data', (chunk: string) => {
			pulse.fire();
			output += chunk;
			outputChannel.write(chunk);
			if (options.resolveOn && options.resolveOn.exec(output)) {
				resolve({
					cmdOutput: output,
				});
			}
		});
		p.on('exit', (code: number) => {
			try {
				if (code) {
					reject({
						message: `Command failed: ${cmd} ${args.join(' ')}`,
						cmdOutput: output,
						code
					});
				} else {
					resolve({
						cmdOutput: output,
					});
				}
			} catch (e) {
				reject(e);
			}
		});
	});
	result.then(() => pulse.dispose(), () => pulse.dispose());
	return { result, pulse: pulse.event };
}

export async function findWindowsExecutable(command: string, cwd: string, env?: Record<string, string | undefined>): Promise<string> {
	if (process.platform !== 'win32') {
		return command;
	}

	// From terminalTaskSystem.ts.

	// If we have an absolute path then we take it.
	if (path.isAbsolute(command)) {
		return command;
	}
	let dir = path.dirname(command);
	if (dir !== '.') {
		// We have a directory and the directory is relative (see above). Make the path absolute
		// to the current working directory.
		return path.join(cwd, command);
	}
	let paths: string[] | undefined = undefined;
	// The options can override the PATH. So consider that PATH if present.
	if (env) {
		// Path can be named in many different ways and for the execution it doesn't matter
		for (let key of Object.keys(env)) {
			if (key.toLowerCase() === 'path') {
				const value = env[key];
				if (typeof value === 'string') {
					paths = value.split(path.delimiter);
				}
				break;
			}
		}
	}
	// No PATH environment. Make path absolute to the cwd.
	if (paths === void 0 || paths.length === 0) {
		return path.join(cwd, command);
	}
	// We have a simple file name. We get the path variable from the env
	// and try to find the executable on the path.
	for (let pathEntry of paths) {
		// The path entry is absolute.
		let fullPath: string;
		if (path.isAbsolute(pathEntry)) {
			fullPath = path.join(pathEntry, command);
		} else {
			fullPath = path.join(cwd, pathEntry, command);
		}
		if (await exists(fullPath)) {
			return fullPath;
		}
		let withExtension = fullPath + '.com';
		if (await exists(withExtension)) {
			return withExtension;
		}
		withExtension = fullPath + '.exe';
		if (await exists(withExtension)) {
			return withExtension;
		}
	}
	return path.join(cwd, command);
}

export async function logEvents(docker: Docker, containerId: string, file: string) {
	const stream = await docker.getEvents({ filters: { container: [containerId] } });
	stream.on('data', data => {
		fs.appendFile(file, data, err => {
			if (err) {
				console.error(err);
			}
		});
	});
}

export async function eventOrStatus(docker: Docker, containerId: string, event: 'kill' | 'die') {
	const stream = await docker.getEvents({
		filters: {
			container: [containerId],
			event: [event],
		}
	});
	return new Promise<void>(resolve => {
		stream.on('data', () => {
			stream.removeAllListeners();
			resolve();
		});
		docker.getContainer(containerId).inspect().then(info => {
			if (event === 'kill' && !info.State.Running) {
				resolve();
			} else if (event === 'die' && info.State.Dead) {
				resolve();
			}
		}, err => {
			if (event === 'die' && err && err.statusCode === 404) {
				resolve();
			}
		});
	});
}

export async function startEventSeen(docker: Docker, label: string, labelValue: string, canceled: Promise<void>) {
	const stream = await docker.getEvents({ filters: { status: ['started'] } });
	return {
		started: new Promise((resolve, reject) => {
			canceled.catch(err => {
				stream.removeAllListeners();
				reject(err);
			});
			stream.on('data', async data => {
				try {
					if (data) {
						for (const part of data.toString().split('\n')) {
							if (part.trim()) {
								const info = JSON.parse(part);
								if (info.status === 'start' && info.Actor && info.Actor.Attributes && info.Actor.Attributes[label] === labelValue) {
									stream.removeAllListeners();
									resolve();
								}
							}
						}
					}
				} catch (e) {
					// Ignore invalid JSON.
					console.error(e);
					console.error(data.toString());
				}
			});
		})
	};
}

export async function showProgress<T>(params: ResolverParameters, message: string, promise: Promise<T>, _pulse: vscode.Event<void>) {
	params.progress.report({ message });
	try {
		return await promise;
	} catch (err) {
		params.output.write(`[1m[31mFailed: ${message}[39m[22m\r\n`);
		throw err;
	}
}

export function printPortInfo(output: vscode.TerminalRenderer, _hostPort: number, container: Docker.ContainerInfo) {
	const lines = [
		...(container.Ports.length ? [`Published Ports:`] : []),
		...container.Ports.map(port => `${port.PrivatePort}/${port.Type} -> ${port.IP}:${port.PublicPort}`),
		...(container.Ports.length ? [``] : [])
	];
	const max = lines.reduce((max, line) => Math.max(line.length, max), 0);
	const bar = '-'.repeat(max);
	if (max > 0) {
		output.write(`\r\n\r\n${bar}\r\n${lines.join('\r\n')}${bar}\r\n\r\n`);
	}
}

export async function ensureDockerRunning() {
	while (true) {
		try {
			await exec(`docker system info`);
			break;
		} catch (err) {
			throw new ContainerError({
				description: 'Docker returned an error. Make sure the Docker daemon is running and select an option how to proceed.',
				originalError: `Docker returned an error code ${err.error.code}, signal ${err.error.signal}, message: ${err.error.message}`
			});
		}
	}
}

export interface ExecResult {
	error: Error | null;
	stdout: string;
	stderr: string;
}


export function exec(command: string, options: cp.ExecOptions & { stdin?: string } = {}) {
	return new Promise<ExecResult>((resolve, reject) => {
		const child = cp.exec(command, options, (error, stdout, stderr) => {
			(error ? reject : resolve)({ error, stdout, stderr });
		});
		if (options.stdin) {
			child.stdin.write(options.stdin, (err: any) => {
				if (err) {
					console.error(err);
				}
				child.stdin.end((err: any) => {
					if (err) {
						console.error(err);
					}
				});
			});
		}
	});
}

export async function inspectImage(params: ResolverParameters, imageName: string) {
	try {
		return await params.docker.getImage(imageName)
			.inspect();
	} catch (err) {
		const { result, pulse } = await runCommand(params.cwd, 'docker', ['pull', imageName], params.output);
		await showProgress(params, `Downloading image '${imageName}'.`, result, pulse);
		return params.docker.getImage(imageName)
			.inspect();
	}
}

export async function findDevContainerConfigPath(folderPath: string) {
	return (await getDevContainerConfigPathIn(folderPath))
		|| (await findReadyMadeConfigPath(folderPath));
}

export function getDefaultDevContainerConfigPath(folderPath: string) {
	return path.join(folderPath, '.devcontainer.json');
}

export async function getDevContainerConfigPathIn(folderPath: string) {
	const possiblePaths = [
		path.join(folderPath, '.devcontainer', 'devcontainer.json'),
		path.join(folderPath, '.devcontainer.json'),
	];

	for (let possiblePath of possiblePaths) {
		if (await exists(possiblePath)) {
			return possiblePath;
		}
	}

	return undefined;
}

async function findReadyMadeConfigPath(folderPath: string): Promise<string | undefined> {
	let stdout: string;
	try {
		stdout = (await exec('git remote -v', { cwd: folderPath })).stdout;
	} catch {
		return undefined;
	}

	const matches = Array.from(new Set(allMatches(/^[^\s]+\s+([^\s]+)/gm, stdout, 1)));
	const repoPaths = new Set(matches.map(extractRepoPath).filter(p => !!p) as string[]);
	for (const repoPath of repoPaths) {
		const folder = path.join(__dirname, 'node_modules', 'vscode-dev-containers', 'repository-containers', repoPath);
		if (await exists(folder)) {
			const configPath = await getDevContainerConfigPathIn(folder);
			if (configPath) {
				return configPath;
			}
		}
	}

	return undefined;
}

function extractRepoPath(url: string) {
	let repo: string | undefined;
	if (url.startsWith('https://')) {
		repo = url.substr(8);
	} else {
		const m = /[^@:]+@([^:]+):(.+)/.exec(url);
		if (m) {
			repo = `${m[1]}/${m[2]}`;
		} else {
			return undefined;
		}
	}
	if (repo.endsWith('.git')) {
		repo = repo.substr(0, repo.length - 4);
	}
	return repo;
}

function allMatches(regex: RegExp, string: string, group: number) {
	return {
		[Symbol.iterator]: function* () {
			let m: RegExpExecArray | null;
			while (m = regex.exec(string)) {
				yield m[group];
				if (regex.lastIndex === m.index) {
					regex.lastIndex++;
				}
			}
		}
	};
}

export function getHostPathFromAuthority(authority: string) {
	const parts = authority.split('+');
	return parts[0] === 'dev-container' ? Buffer.from(parts[1], 'hex').toString('utf8') : undefined;
}

export function getDevContainerUri(hostPath: string, containerPath: string) {
	return vscode.Uri.parse(`vscode-remote://dev-container+${Buffer.from(hostPath, 'utf8').toString('hex')}${containerPath}`);
}

export async function getDevContainerPaths(folderPath: string) {
	let hostMountFolder = folderPath;
	try {
		const { stdout } = await exec('git rev-parse --show-toplevel', { cwd: folderPath });
		hostMountFolder = stdout.trim();
	} catch (err) {
		// ignore
	}
	const rel = path.relative(path.dirname(hostMountFolder), folderPath);
	return {
		hostWorkspaceFolder: folderPath,
		containerWorkspaceFolder: `/workspaces/${process.platform === 'win32' ? rel.replace(/\\/g, '/') : rel}`,
		hostMountFolder,
		containerMountFolder: `/workspaces/${path.basename(hostMountFolder)}`
	};
}

export function tildify(platform: typeof process.platform, path: string, userHome: string): string {
	if (platform === 'win32') {
		return path; // unsupported
	}

	let normalizedUserHome = `${rtrim(userHome, '/')}${'/'}`;

	// Linux: case sensitive, macOS: case insensitive
	if (platform === 'linux' ? path.startsWith(normalizedUserHome) : path.toLowerCase().startsWith(normalizedUserHome.toLowerCase())) {
		path = `~/${path.substr(normalizedUserHome.length)}`;
	}

	return path;
}

function rtrim(haystack: string, needle: string) {
	if (needle.length === 0) {
		return haystack;
	}

	while (haystack.endsWith(needle)) {
		haystack = haystack.substr(0, haystack.length - needle.length);
	}

	return haystack;
}

export function getConfigFilePath(_workspaceFolderPath: string, config: DevContainerFromImageConfig | DevContainerFromDockerfileConfig | DevContainerFromDockerComposeConfig, fileConfig: string) {
	return path.resolve(path.dirname(config.configFilePath), fileConfig);
}