import * as Docker from 'dockerode';
import * as vscode from 'vscode';

import { defaultDevPort, containerRemoteContext, ResolverParameters, ensureDockerRunning, printPortInfo, ResolverResult } from './resolverUtils';
import { getInstallLocation, installVSCHeadless, launchVSCHeadless, getNodeLocation, getHomedirFromContainer } from './injectHeadless';
import { forwardPort, ForwardedPort } from './portForwarding';

import { promisify } from 'util';
import { getProduct } from './product';
import { handleError, ContainerError, documentation, reload, openRecent, genericActions } from './errors';

export async function resolve(authority: string): Promise<ResolverResult> {

	const output = vscode.window.createTerminalRenderer('Dev Containers');
	const reveal = 'openindocker.revealLogTerminal';
	vscode.commands.registerCommand(reveal, () => output.terminal.show());

	return vscode.window.withProgress({
		location: vscode.ProgressLocation.Notification,
		title: `Installing Dev Container ([details](command:${reveal} "Show details in terminal"))`,
		cancellable: false
	}, async (progress) => {
		try {

			const segments = authority.split('+');
			const containerId = Buffer.from(segments[1], 'hex').toString('utf8');

			output.write(`Setting up container with ${containerId}\n\r`);

			await ensureDockerRunning();

			const params: ResolverParameters = {
				docker: new Docker(),
				authority,
				cwd: process.cwd(),
				progress,
				output,
				shutdowns: [],
				forwardedPorts: new Set<ForwardedPort>()
			};

			params.docker.getContainer(containerId)
				.inspect()
				.then(info => {
					const subs = vscode.workspace.registerResourceLabelFormatter({
						scheme: 'vscode-remote',
						authority,
						formatting: {
							label: '${path}',
							separator: '/',
							workspaceSuffix: `Attached Container ${info.Config.Image} (${info.Name})`,
							tildify: true
						}
					});
					params.shutdowns.push(subs.dispose.bind(subs));
				})
				.catch(console.error);

			const container = await findTargetContainerFromId(params.docker, containerId);
			if (!container) {
				throw new ContainerError({ description: `Cannot attach to the container with id ${containerId}, it no longer exists.`, data: { reload: false } });
			}
			params.containerId = container.Id;
			const resolvedAuthority = await attachToContainer(params, container);
			return { authority, params, resolvedAuthority };
		} catch (err) {
			const attachErr = err instanceof ContainerError ? err : new ContainerError({
				description: 'An error occurred attaching to the container.',
				originalError: err
			});
			attachErr.actions = [
				...genericActions,
				reload,
				openRecent,
				documentation,
			];
			throw attachErr;
		}
	})
		.then(undefined, err => {
			const attachErr = err instanceof ContainerError ? err : new ContainerError({
				description: 'An error occurred attaching to the container.',
				originalError: err
			});
			return handleError(attachErr, true, output);
		});
}

async function attachToContainer(params: ResolverParameters, container: Docker.ContainerInfo): Promise<vscode.ResolvedAuthority> {
	const containerId = container.Id;
	const { commit } = await getProduct();

	if (!commit) {
		throw new ContainerError({ description: 'Cannot attach without commit id for the remote extension host.' });
	}

	if (container.State !== 'running') {
		throw new ContainerError({ description: `Cannot attach to the container with id ${containerId}, it is not running.` });
	}

	const homedir = await getHomedirFromContainer(params.docker, containerId);
	const installLocation = await getInstallLocation(homedir);
	const nodeLocation = await getNodeLocation(homedir);

	await installVSCHeadless(params, containerId, installLocation, homedir);
	await launchVSCHeadless(params, containerId, installLocation, homedir, defaultDevPort);

	const server = await forwardPort(params.forwardedPorts, params.cwd, containerId, defaultDevPort, nodeLocation);
	const hostPort = server.address().port;

	printPortInfo(params.output, hostPort, container);

	const shutdown = async () => {
		vscode.commands.executeCommand('setContext', containerRemoteContext, false);
		await promisify(server.close.bind(server))();
	};

	vscode.commands.executeCommand('setContext', containerRemoteContext, true);
	params.shutdowns.push(shutdown);
	return new vscode.ResolvedAuthority('localhost', hostPort);
}

async function findTargetContainerFromId(docker: Docker, containerId: string): Promise<Docker.ContainerInfo | undefined> {
	const list = await docker.listContainers({ all: true, filters: { id: [containerId] } });
	return list && list[0];
}
