import * as vscode from 'vscode';
import { exec } from './resolverUtils';

export async function checkDockerVersion() {
	const check = await hasDockerVersion();
	if (check === 'permission') {
		const result = await vscode.window.showErrorMessage('Current user does not have permission to run \'docker\'. Try adding the user to the \'docker\' group.', { modal: true }, 'Documenation');
		if (result) {
			vscode.env.openExternal(vscode.Uri.parse('https://go.microsoft.com/fwlink/?linkid=2079224'));
		}
		return false;
	}
	if (check === 'daemon') {
		await vscode.window.showErrorMessage('Docker returned an error. Make sure the Docker daemon is running.', { modal: true });
		return false;
	}
	if (!check || check === 'command') {
		const result = await vscode.window.showErrorMessage('Docker version 17.06.1 or later required', { modal: true }, 'Go to docker.com');
		if (result) {
			vscode.env.openExternal(vscode.Uri.parse('https://www.docker.com'));
		}
		return false;
	}
	return true;
}

export async function hasDockerVersion() {
	let stdout: string;
	try {
		const result = await exec(`docker version --format '{{.Server.Version}}'`);
		stdout = result.stdout;
	} catch (err) {
		console.error(err);
		const stderr = err.stderr ? err.stderr.toLowerCase() : '';
		if (stderr.indexOf('permission denied') !== -1) {
			return 'permission';
		}
		if (stderr.indexOf('command not found') !== -1 || stderr.indexOf('not recognized as an internal or external command') !== -1) {
			return 'command';
		}
		return 'daemon';
	}
	const m = /^\d+(\.\d+)*/.exec(stdout.trim());
	if (!m) {
		return true; // Optimistically continue.
	}
	const version = m[0].split('.')
		.map(i => parseInt(i, 10));
	return !isEarlierVersion(version, [17, 5, 0]); // 17.05.0-ce or 17.06.1-ee-1
}

function isEarlierVersion(left: number[], right: number[]) {
	for (let i = 0, n = Math.max(left.length, right.length); i < n; i++) {
		const l = left[i] || 0;
		const r = right[i] || 0;
		if (l !== r) {
			return l < r;
		}
	}
	return false; // Equal.
}

export async function checkDockerCompose() {
	try {
		await exec(`docker-compose --version`);
		return true;
	} catch (err) {
		const result = await vscode.window.showErrorMessage('Docker Compose required', { modal: true }, 'Documentation');
		if (result) {
			vscode.env.openExternal(vscode.Uri.parse('https://go.microsoft.com/fwlink/?linkid=2079610'));
		}
		return false;
	}
}
