import * as vscode from 'vscode';
import * as Docker from 'dockerode';
import { eventOrStatus } from './resolverUtils';

interface ContainerErrorAction {
	readonly title: string;
	readonly isCloseAffordance?: boolean;
	readonly isLastAction?: boolean;
	applicable: (err: ContainerError, primary: boolean) => boolean;
	execute: (err: ContainerError) => Promise<void>;
}

export const documentation: ContainerErrorAction = {
	title: 'Open Documentation',
	applicable: (_: ContainerError, primary: boolean) => !primary,
	execute: async (_: ContainerError) => {
		await vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/container/resolve-failed'));
	}
};

export const reload: ContainerErrorAction = {
	title: 'Reload Window',
	applicable: (err: ContainerError, primary: boolean) => !primary && err.data.reload !== false,
	execute: async (err: ContainerError) => {
		if (err.manageContainer && err.container) {
			const docker = new Docker();
			const cont = await docker.getContainer(err.container.Id);
			const stopped = eventOrStatus(docker, cont.id, 'kill');
			cont.stop({ t: 0 }); // Don't 'await' since this would fail if the container is already stopped.
			await stopped;
		}

		await vscode.commands.executeCommand('workbench.action.reloadWindow');
	}
};

export const rebuild: ContainerErrorAction = {
	title: 'Rebuild Container',
	applicable: (err: ContainerError, primary: boolean) => err.manageContainer && !!err.container && !primary,
	execute: async (err: ContainerError) => {
		const docker = new Docker();
		const cont = await docker.getContainer(err.container!.Id);
		const removed = eventOrStatus(docker, cont.id, 'die');
		await cont.remove({ force: true });
		await removed;
		await vscode.commands.executeCommand('workbench.action.reloadWindow');
	}
};

export const openRecent: ContainerErrorAction = {
	title: 'Open Recent...',
	applicable: (_: ContainerError, primary: boolean) => !primary,
	execute: async (_: ContainerError) => {
		await vscode.commands.executeCommand('workbench.action.openRecent');
	}
};

export const genericActions: ContainerErrorAction[] = [
	{
		title: 'Retry',
		isLastAction: true,
		applicable: (err: ContainerError, primary: boolean) => primary && err.data.reload !== false,
		execute: reload.execute
	},
	{
		title: 'Close Remote',
		isLastAction: true,
		applicable: (_err: ContainerError, primary: boolean) => {
			if (!primary) {
				return false;
			}
			const file = vscode.workspace.workspaceFile;
			if (file && file.scheme === 'untitled') {
				return false;
			}
			const documents = vscode.workspace.textDocuments;
			if (documents.some(document => document.isDirty)) {
				return false;
			}
			return true;
		},
		execute: async (_: ContainerError) => {
			await vscode.commands.executeCommand('vscode.newWindow', { reuseWindow: true });
		}
	},
	{
		title: 'More Actions...',
		isCloseAffordance: true,
		applicable: (_err: ContainerError, primary: boolean) => primary,
		execute: async (_: ContainerError) => undefined
	},
];

interface ContainerErrorData {
	reload?: boolean;
}

interface ContainerErrorInfo {
	description: string;
	originalError?: any;
	manageContainer?: boolean;
	container?: Docker.ContainerInfo;
	actions?: ContainerErrorAction[];
	data?: ContainerErrorData;
}

export class ContainerError extends Error implements ContainerErrorInfo {

	description!: string;
	originalError?: any;
	manageContainer = false;
	container?: Docker.ContainerInfo;
	actions: ContainerErrorAction[] = [documentation];
	data: ContainerErrorData = {};

	constructor(info: ContainerErrorInfo) {
		super(info.description);
		Object.assign(this, info);
	}
}

export function bailOut(output: vscode.TerminalRenderer, message: string): never {
	output.write(`[1m[31m${message}[39m[22m\n`);
	throw new Error(message);
}

export async function handleError(error: any, throwNotAvailable: true, renderer?: vscode.TerminalRenderer): Promise<never>;
export async function handleError(error: any, throwNotAvailable?: false, renderer?: vscode.TerminalRenderer): Promise<void>;
export async function handleError(error: any, throwNotAvailable = false, renderer?: vscode.TerminalRenderer) {

	const err = error instanceof ContainerError ? error : new ContainerError({
		description: 'An error occurred setting up the container.',
		originalError: error
	});

	if (renderer) {
		renderer.terminal.show();
		const logErr = err.originalError && (err.originalError.stack || err.originalError.message) || err.originalError;
		if (logErr !== undefined) {
			const str: string = String(logErr);
			const out = toErrorText(str);
			renderer.write(out);
		}
	}

	const primaryActions = err.actions.filter(action => action.applicable(err, true));
	const result = await vscode.window.showErrorMessage(err.description, { modal: true }, ...primaryActions);
	if (result) {
		await result.execute(err);
	}

	if (result && result.isLastAction) {
		if (throwNotAvailable) {
			throw vscode.RemoteAuthorityResolverError.NotAvailable(undefined, true);
		}
		return;
	}

	const secondaryActions = err.actions.filter(action => action.applicable(err, false));
	const picks = secondaryActions.map(action => ({
		label: action.title,
		action
	}));

	async function showSecondaryActions() {
		const picker = secondaryActions.length ? vscode.window.showQuickPick(picks, {
			ignoreFocusOut: true,
			placeHolder: 'Pick an action to help resolve the problem.'
		}) : Promise.resolve(undefined);
		const pick = await picker;
		if (pick) {
			pick.action.execute(err)
				.catch(console.error);
			await showSecondaryActions();
		}
	}
	showSecondaryActions()
		.catch(console.error);

	if (throwNotAvailable) {
		throw vscode.RemoteAuthorityResolverError.NotAvailable(undefined, true);
	}
}

export function toErrorText(str: string) {
	return str.split('\n')
		.map(line => `[1m[31m${line}[39m[22m\r`)
		.join('\n') + '\r\n';
}
