import * as vscode from 'vscode';
import * as Docker from 'dockerode';

import { ResolverResult } from './resolverUtils';
import { getHomedirFromContainer, getUserVSCodeLocation, getNodeLocation } from './injectHeadless';
import { ForwardedPort, forwardPort } from './portForwarding';
import { findListeningPorts } from './proc';

interface PortItem {
	static: boolean;
	label: string;
	description: string;
	port: number;
	localPort?: number;
	forwardedPort?: ForwardedPort;
}

export async function forwardPortUI(resolverResults: Map<string, ResolverResult>) {
	const result = [...resolverResults.values()][0];
	if (result && result.params.containerId) {
		const params = result.params;
		const containerId = params.containerId!;
		const homedir = await getHomedirFromContainer(params.docker, containerId);
		const containerInfo = await findTargetContainerFromId(params.docker, containerId);
		const staticPorts: Docker.Port[] = containerInfo ? containerInfo.Ports : [];
		const vscodeLocation = await getUserVSCodeLocation(homedir);
		const vscodeLocationSlash = vscodeLocation + '/';
		const port = await vscode.window.showQuickPick(findListeningPorts(params.cwd, containerId)
			.then(listeningPorts => {
				const forwardedPorts = [...params.forwardedPorts].reduce((m, c) => m.set(c.targetPort, c), new Map<number, ForwardedPort>());
				const list: PortItem[] = listeningPorts.filter(port =>
					!port.process.cmd.startsWith(vscodeLocationSlash)
					&& port.process.cwd !== vscodeLocation
					&& !port.process.cwd.startsWith(vscodeLocationSlash)
					&& !staticPorts.find(staticPort => staticPort.PrivatePort === port.port))
					.map(port => {
						const forwardedPort = forwardedPorts.get(port.port);
						return {
							static: false,
							label: forwardedPort ? `Stop Forwarding ${forwardedPort.server.address().port}  ${port.port}` : `Forward ${port.port}`,
							description: port.process.cmd,
							port: port.port,
							localPort: forwardedPort && forwardedPort.server.address().port,
							forwardedPort
						};
					});
				list.push(...staticPorts.map(staticPort => {
					const listeningPort = listeningPorts.find(listeningPort => listeningPort.port === staticPort.PrivatePort);
					return {
						static: true,
						label: `Forwarding ${staticPort.PublicPort}  ${staticPort.PrivatePort}`,
						description: listeningPort ? listeningPort.process.cmd : 'configured in devcontainer.json',
						port: staticPort.PrivatePort,
						localPort: staticPort.PublicPort,
					};
				}));
				list.sort((a, b) => a.port - b.port);
				list.push({
					static: false,
					label: `Learn More`,
					description: `about port forwarding`,
					port: -1,
				});
				return list.length ? list : [{ label: 'No listening ports found' }];
			}) as Thenable<PortItem[]>, {
				placeHolder: 'Select a port to start/stop forwarding to'
			});
		if (port && port.port) {
			if (port.forwardedPort) {
				const forwardedPort = (port.forwardedPort as ForwardedPort);
				forwardedPort.server.close();
				vscode.window.showInformationMessage(`Stopped forwarding port ${forwardedPort.targetPort}`);
			} else if (port.port === -1) {
				vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/containers/ports'));
			} else if (port.forwardedPort) {
				(port.forwardedPort as ForwardedPort).server.close();
			} else {
				const open = 'Open Browser';
				const copy = 'Copy Local Address';
				let res: string | undefined;
				let localPort: number;
				if (port.static) {
					localPort = port.localPort!;
					res = await vscode.window.showInformationMessage(`Port ${localPort} is statically forwarding to port ${port.port} in the container.`, copy, open);
				} else {
					const nodeLocation = await getNodeLocation(homedir);
					const server = await forwardPort(params.forwardedPorts, params.cwd, containerId, port.port, nodeLocation);
					localPort = server.address().port;
					res = await vscode.window.showInformationMessage(`Port ${localPort} is now forwarding to port ${port.port} in the container.`, copy, open);
				}
				if (res === open) {
					await vscode.env.openExternal(vscode.Uri.parse(`http://localhost:${localPort}`));
				} else if (res === copy) {
					await vscode.env.clipboard.writeText(`localhost:${localPort}`);
				}
			}
		}
	}
}

async function findTargetContainerFromId(docker: Docker, containerId: string): Promise<Docker.ContainerInfo | undefined> {
	const list = await docker.listContainers({ all: true, filters: { id: [containerId] } });
	return list && list[0];
}
