import * as path from 'path';

import * as jsonc from 'jsonc-parser';
import * as vscode from 'vscode';
import * as Docker from 'dockerode';

import { readFile, exists } from './pfs';
import { DevContainerFromImageConfig, DevContainerFromDockerfileConfig, openDockerfileDevContainer } from './singleContainer';
import { DevContainerFromDockerComposeConfig, openDockerComposeDevContainer } from './dockerCompose';
import { ensureDockerRunning, ResolverParameters, findDevContainerConfigPath, getHostPathFromAuthority, getDefaultDevContainerConfigPath, IOpenFolderAPICommandOptions, ResolverResult } from './resolverUtils';
import { substitute } from './variableSubstitution';
import { ContainerError, handleError, documentation, reload, rebuild, openRecent, genericActions } from './errors';
import { ForwardedPort } from './portForwarding';

export type DevContainerConfig = DevContainerFromImageConfig | DevContainerFromDockerfileConfig | DevContainerFromDockerComposeConfig;

export async function resolve(authority: string): Promise<ResolverResult> {

	const folderPath = getHostPathFromAuthority(authority)!;

	let output = vscode.window.createTerminalRenderer('Dev Containers');
	const reveal = 'openindocker.revealLogTerminal';
	vscode.commands.registerCommand(reveal, () => output.terminal.show());
	return vscode.window.withProgress({
		location: vscode.ProgressLocation.Notification,
		title: `Installing Dev Container ([details](command:${reveal} "Show details in terminal"))`,
		cancellable: false
	}, async progress => {

		output.write(`Setting up container for folder: ${folderPath}\n\r`);

		await ensureDockerRunning();

		const configPath = await findDevContainerConfigPath(folderPath);
		const config = configPath && await readDevContainerConfigFile(configPath);
		if (!config) {
			throw new ContainerError({ description: `Dev container config (${configPath || getDefaultDevContainerConfigPath(folderPath)}) not found.` });
		}

		const params: ResolverParameters = {
			docker: new Docker(),
			authority,
			cwd: folderPath,
			progress,
			output,
			shutdowns: [],
			forwardedPorts: new Set<ForwardedPort>()
		};

		const subs = vscode.workspace.registerResourceLabelFormatter({
			scheme: 'vscode-remote',
			authority,
			formatting: {
				label: '${path}',
				separator: '/',
				workspaceSuffix: config.name ? `Dev Container: ${config.name}` : 'Dev Container',
				tildify: true
			}
		});
		params.shutdowns.push(subs.dispose.bind(subs));

		if ('dockerFile' in config || 'image' in config) {
			const resolvedAuthority = await openDockerfileDevContainer(params, config);
			return { authority, params, resolvedAuthority };
		} else if ('dockerComposeFile' in config) {
			const resolvedAuthority = await openDockerComposeDevContainer(params, config);
			return { authority, params, resolvedAuthority };
		}

		throw new ContainerError({ description: `Dev container config (${configPath}) not valid.` });
	})
		.then(undefined, err => {
			const buildErr = err instanceof ContainerError ? err : new ContainerError({
				description: 'An error occurred setting up the container.',
				originalError: err
			});
			buildErr.actions = [
				...genericActions,
				reload,
				rebuild,
				{
					title: 'Open Folder Locally',
					applicable: (_, primary) => !primary,
					async execute() {
						const uri = vscode.Uri.file(folderPath);
						const options: IOpenFolderAPICommandOptions = {
							forceNewWindow: true,
						};
						await vscode.commands.executeCommand('vscode.openFolder', uri, options);
					}
				},
				openRecent,
				documentation,
			];
			const cmdOutput: string | undefined = buildErr.originalError && buildErr.originalError.cmdOutput;
			if (cmdOutput && cmdOutput.toLowerCase().indexOf('drive has not been shared') !== -1) {
				buildErr.description = 'The drive of the workspace folder is not shared. Share it in the Docker settings first.';
			} else if (cmdOutput && cmdOutput.toLowerCase().indexOf('port is already allocated') !== -1) {
				buildErr.description = 'A port the container is setup to bind to is already in use. See the Dev Containers terminal for details.';
			} else if (cmdOutput && cmdOutput.toLowerCase().indexOf('deb.debian.org/debian/dists/jessie-updates') !== -1) {
				buildErr.description = 'The base image references the obsolete Debian 8 update repository.';
				buildErr.actions.push({
					title: 'Learn More',
					applicable: (_, primary) => primary,
					async execute() {
						await vscode.env.openExternal(vscode.Uri.parse('https://aka.ms/vscode-remote/containers/troubleshooting/jessie'));
					}
				});
			}
			return handleError(buildErr, true, output);
		});
}

export async function readDevContainerConfigFile(filePath: string): Promise<DevContainerConfig | { configFilePath: string; name?: string; extensions?: string; } | undefined> {
	if (!await exists(filePath)) {
		return undefined;
	}

	const buf = await readFile(filePath);
	const config: DevContainerConfig = substitute(path.basename(filePath), jsonc.parse(buf.toString()));
	config.configFilePath = filePath;
	return config;
}
